import { useState } from "react"
import { ChipLarge, Footer, Header } from "../components"
import DiaryThreapist from "./Diary/DiaryThreapist"
import ParentsReview from "./Diary/ParentsReview"
import ThreapistReview from "./Diary/ThreapistReview"

interface DiaryProps {
}

const DiaryChild: React.FC<DiaryProps> = (props) => {
    const [menu, setMenu] = useState(1);
    const [menuArray, setMenuArray] = useState([0, 1, 0, 0, 0]);
    const changeMenu = (num: number) => {
        setMenu(() => num);
        const newArr = [...menuArray];
        menuArray.map((item: number, index: number) => {
            if (num == index) { newArr[index] = 1; }
            else { newArr[index] = 0; }
            setMenuArray(() => newArr);
        });
    }
    const clickMenu = (num: number) => {
        changeMenu(num);
    };
    let component = <DiaryThreapist></DiaryThreapist>;
    switch (menu) {
        case 1: component = (<DiaryThreapist></DiaryThreapist>); break;
        case 2: component = (<ThreapistReview></ThreapistReview>); break;
        case 3: component = (<ParentsReview></ParentsReview>); break;
        default: component = (<DiaryThreapist></DiaryThreapist>); break;
    }
    return (
        <div className="common-bg-myinfo">
            <Header
                className="group-add-header"
                goodroad="/asset/img/subpage_top_tabbar_logo.svg"
                person="/asset/img/new_classify_icon_01.svg"
                bell="/asset/img/bell.svg"
                kakao="/asset/img/subpage_top_tabbar_icon_message.svg"
                color="font-bold-s12-cffffff"
            />
            <div className="media-wrapper">
                <div className="common-wrapper__container"></div>
                <div className="common-wrapper__container">
                    {/* ######################### Content Area Begin ######################### */}
                    <img src="/asset/img/treatment_main.svg" style={{ marginTop: 20 }} />
                    <div className="common-flex-row-wrap-vcenter" style={{ marginTop: 20, marginLeft: 10 }}>
                        <ChipLarge>임시메뉴</ChipLarge>
                        <ChipLarge onClick={() => clickMenu(1)} type={menuArray[1]}>치료일지:치료사</ChipLarge>
                        <ChipLarge onClick={() => clickMenu(3)} type={menuArray[3]}>보호자후기:치료사</ChipLarge>
                        <ChipLarge onClick={() => clickMenu(2)} type={menuArray[2]}>치료사후기:보호자</ChipLarge>
                    </div>
                    {component}
                    <div className="media-blank-bottom"></div>
                    {/* ######################### Content Area End   ######################### */}
                </div>
                <div className="common-wrapper__container"></div>
                <Footer></Footer>
            </div>
        </div>
    )
}

export default DiaryChild