import { LocalizationProvider, StaticDatePicker, DesktopDatePicker, PickersDay, pickersDayClasses, PickersDayProps } from "@mui/lab";
import AdapterDateFns from "@mui/lab/AdapterDateFns";
import { Box, TextField, Avatar, FormControlLabel, Checkbox, InputBase, Modal, RadioGroup, Radio, Input, Badge, Button, Tooltip } from "@mui/material";
import { ko } from "date-fns/locale";
import moment from "moment";
import { useEffect, useRef, useState } from "react";
import ButtonSquareLineB from "../../../components/ButtonSquareLineB";
import ButtonSquareSmall from "../../../components/ButtonSquareSmall";
import FormErrorMessage from "../../../components/FormErrorMessage";
import { profileValidation } from "../../../api/yup";
import DragDrop from "./DragDrop";
import { initialProfile, Profile } from "../../../api/atom.myinfo";
import { yupResolver } from '@hookform/resolvers/yup';
import { useForm } from 'react-hook-form';

interface Props {

}


const MyInfoEditor: React.FC<Props> = (props) => {
    moment.locale('ko');
    const [edit, setEdit] = useState(false);
    const onEdit = () => {
        setEdit(() => !edit);
    }
    //-- modal
    const [open, setOpen] = useState(false);
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);
    //-- modal
    const [profile, setProfile] = useState(initialProfile);
    //--- gender
    const [genderValue, setGenderValue] = useState('female');
    const handleChangeGender = (event: React.ChangeEvent<HTMLInputElement>) => {
        setGenderValue(() => event.target.value);
    };
    useEffect(() => {
        setProfile({ ...profile, "gender": genderValue });
    }, [genderValue])
    //--- gender
    //--- 공개/비공개
    const [openValue, setOpenValue] = useState('close');
    const handleChangeOpen = (event: React.ChangeEvent<HTMLInputElement>) => {
        setOpenValue(() => event.target.value);
    };
    useEffect(() => {
        setProfile({ ...profile, "open": openValue });
    }, [openValue])
    //--- 공개/비공개
    const { handleSubmit, formState: { errors }, register } = useForm<Profile>({
        resolver: yupResolver(profileValidation),
        mode: 'onBlur',
    });
    const [errorMsg, setErrorMsg] = useState("");
    const onSubmit = handleSubmit((data: Profile) => {
        if (tmpBirth == "") {
            setErrorMsg("생일을 입력해주세요.");
        } else {
            setErrorMsg("");
            onEdit();
            console.log("Profile", data);
        }
    });
    const onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const { name, value } = e.target;
        console.log("name", name);
        setProfile({ ...profile, [name]: value });
    }
    useEffect(() => {
        setGenderValue("male");
        setOpenValue("open");
    }, [edit])
    //--- birth
    const [birth, setBirth] = useState<Date | null>(null);
    const [tmpBirth, setTmpBirth] = useState("");
    const handleBirthDateChange = (date: Date | null) => {
        console.log("handleBirthDateChange >>>> date", date);
        if (date != null) {
            setBirth(() => date);
        }
        setTmpBirth(() => moment(date).format('YYYY-MM-DD'));
    };
    useEffect(() => {
        setProfile({ ...profile, "birth": tmpBirth });
        console.log("useEffect >>>> tmpBirth", tmpBirth);
    }, [tmpBirth])
    //--- birth

    useEffect(() => {
        console.log("useEffect >>>> profile", profile);
    }, [profile])
    const onSubmitProfile = () => {
        onEdit();
        console.log("useEffect >>>> profile", profile);
    }


    return (
        <>
            <div className="media-container-round-grey-shadow-padding40" style={{ marginTop: 10 }}>
                <div className="common-flex-row-wrap-hcenter-vcenter">
                    <Avatar
                        alt="Remy Sharp"
                        src="/asset/img/mypage_img_photo_edit.svg"
                        sx={{ width: 120, height: 120 }}
                        onClick={() => handleOpen()}
                    />
                    <Modal
                        open={open}
                        onClose={handleClose}
                        aria-labelledby="modal-modal-title"
                        aria-describedby="modal-modal-description"
                    >
                        <Box className="popup-style">
                            <div className="common-flex-row-reverse-wrap-vcenter" style={{ padding: 6 }}>
                                <img src="/asset/img/treatment_icon_x.svg"
                                    onClick={handleClose}
                                    style={{ cursor: "pointer" }} />
                            </div>
                            <span className="font-bold-s18-c573300">사진 올리기</span>
                            <DragDrop></DragDrop>
                            <div className="common-flex-row-wrap-hcenter-vcenter"
                                style={{ marginTop: 10, marginLeft: 20, marginRight: 20 }}>
                                <ButtonSquareSmall>등록</ButtonSquareSmall>
                                <ButtonSquareSmall onClick={handleClose}>취소</ButtonSquareSmall>
                            </div>
                        </Box>
                    </Modal>
                </div>

                <form onSubmit={onSubmit}>
                    <div className="media-container-round-white-shadow-padding40" style={{ marginTop: 10 }}>
                        <div className="media-grid--col2__11">
                            <div className="box-cgc">
                                <div className="common-flex-row-wrap-space-between-vcenter">
                                    <div>
                                        <span className="font-bold-s27-c573300" style={{ marginLeft: 20, marginRight: 20 }}>
                                            내정보
                                        </span>
                                        <img src="/asset/img/myinfo_icon_pencil1.svg" style={{ marginLeft: 0 }} />
                                    </div>
                                    <div style={{ marginRight: 20 }}>
                                        <div className="common-flex-row-reverse-wrap-vcenter" style={{ marginRight: 20 }}>
                                            {
                                                edit === false ?
                                                    <ButtonSquareLineB type={0} onClick={() => onEdit()}>편집</ButtonSquareLineB>
                                                    :
                                                    <ButtonSquareLineB type={1} onClick={() => onSubmit()}>저장</ButtonSquareLineB>
                                            }
                                        </div>
                                    </div>
                                </div>
                                <div className="sign-line"></div>
                                <div className="common-flex-row-wrap-vcenter" style={{ marginLeft: 20, marginRight: 20 }}>
                                    <div className="common-flex-row-wrap-vcenter" style={{ width: 100 }}>
                                        <span className="font-bold-s12-c000000">공개여부</span>
                                        <Tooltip title="비공개 선택 시 회원 검색 결과에서 제외되어 메세지를 전송할 수 없습니다." arrow>
                                            <img src="/asset/img/myinfo_icon_exclamation-circle.svg" />
                                        </Tooltip>

                                    </div>
                                    <div>
                                        <RadioGroup
                                            row
                                            name="controlled-radio-buttons-group"
                                            value={openValue}
                                            onChange={handleChangeOpen}
                                        >
                                            <FormControlLabel  {...register('open')} value="open" control={<Radio size="small" />}
                                                label={<span style={{ fontSize: 13 }}>공개</span>} />
                                            <FormControlLabel  {...register('open')} value="close" control={<Radio size="small" />}
                                                label={<span style={{ fontSize: 13 }}>비공개</span>} />
                                        </RadioGroup>
                                    </div>
                                </div>

                                <div className="common-flex-row-wrap-vcenter" style={{ marginLeft: 20, marginRight: 20, marginTop: 106 }}>
                                    <div style={{ width: 100 }}><span className="font-bold-s12-c000000">이메일*</span></div>
                                    <div>
                                        <InputBase sx={{ ml: 1, flex: 1 }}
                                            placeholder="admin@lumanlanb.com"
                                            {...register('email')}
                                            value={profile?.email || ""}
                                            onChange={onChange}
                                            readOnly={!edit}
                                            inputProps={{ style: { fontSize: 13 } }} // font size of input text
                                        />
                                        {errors.email && (
                                            <FormErrorMessage errorMessage={errors.email.message!} />
                                        )}
                                    </div>
                                </div>

                                <div className="sign-line"></div>
                                <div className="common-flex-row-wrap-vcenter" style={{ marginLeft: 20, marginRight: 20 }}>
                                    <div style={{ width: 100 }}><span className="font-bold-s12-c000000">비밀번호*</span></div>
                                    <div><InputBase sx={{ ml: 1, flex: 1 }}
                                        type="password"
                                        placeholder="**********"
                                        {...register('password')}
                                        value={profile?.password || ""}
                                        onChange={onChange}
                                        readOnly={!edit}
                                        inputProps={{ style: { fontSize: 13 } }} // font size of input text
                                    /> </div>
                                </div>
                                <div className="sign-line"></div>
                                <div className="common-flex-row-wrap-vcenter" style={{ marginLeft: 20, marginRight: 20 }}>
                                    <div style={{ width: 100 }}><span className="font-bold-s12-c000000">비밀번호 확인*</span></div>
                                    <div><InputBase sx={{ ml: 1, flex: 1 }}
                                        type="password"
                                        placeholder="**********"
                                        {...register('password2')}
                                        value={profile?.password2 || ""}
                                        onChange={onChange}
                                        readOnly={!edit}
                                        inputProps={{ style: { fontSize: 13 } }} // font size of input text
                                    /> </div>
                                </div>
                                <div className="sign-line"></div>

                            </div>
                            <div className="box-cgc">



                                <div className="sign-line" style={{ marginTop: 10 }}></div>
                                <div className="common-flex-row-wrap-vcenter" style={{ marginLeft: 20, marginRight: 20 }}>
                                    <div style={{ width: 100 }}><span className="font-bold-s12-c000000">이름*</span></div>
                                    <div>
                                        <InputBase sx={{ ml: 1, flex: 1 }}
                                            placeholder="홍길동"
                                            {...register('name')}
                                            value={profile?.name || ""}
                                            onChange={onChange}
                                            readOnly={!edit}
                                            inputProps={{ style: { fontSize: 13 } }} // font size of input text
                                        />
                                        {errors.name && (
                                            <FormErrorMessage errorMessage={errors.name.message!} />
                                        )}
                                    </div>
                                </div>


                                <div className="sign-line"></div>
                                <div className="common-flex-row-wrap-vcenter" style={{ marginLeft: 20, marginRight: 20 }}>
                                    <div style={{ width: 100 }}><span className="font-bold-s12-c000000">성별*</span></div>
                                    <div>
                                        <RadioGroup
                                            row
                                            name="controlled-radio-buttons-group"
                                            value={genderValue}
                                            onChange={handleChangeGender}
                                        >
                                            <FormControlLabel {...register('gender')} value="female" control={<Radio size="small" />}
                                                label={<span style={{ fontSize: 13 }}>여성</span>} />
                                            <FormControlLabel {...register('gender')} value="male" control={<Radio size="small" />}
                                                label={<span style={{ fontSize: 13 }}>남성</span>} />
                                        </RadioGroup>
                                    </div>
                                </div>

                                <div className="sign-line"></div>
                                <div className="common-flex-row-wrap-vcenter" style={{ marginLeft: 20, marginRight: 20 }}>
                                    <div style={{ width: 100 }}><span className="font-bold-s12-c000000">생년월일*</span></div>

                                    <LocalizationProvider dateAdapter={AdapterDateFns}>
                                        <DesktopDatePicker
                                            label=""
                                            value={birth}
                                            inputFormat={"yyyy-MM-dd"}
                                            mask={"____-__-__"}
                                            {...register('birth')}
                                            onChange={(date) => handleBirthDateChange(date)}
                                            renderInput={({ inputRef, inputProps, InputProps }) => (
                                                <Box sx={{ display: 'flex', alignItems: 'center', flexGrow: 1 }}>
                                                    <input ref={inputRef} {...inputProps} style={{ border: "none", flexGrow: 1, height: 30 }} />
                                                    {InputProps?.endAdornment}
                                                </Box>
                                            )}
                                        />
                                    </LocalizationProvider>
                                    {errorMsg === "" ? <></> : <FormErrorMessage errorMessage={errorMsg} />}
                                </div>
                                <div className="sign-line"></div>
                                <div className="common-flex-row-wrap-vcenter" style={{ marginLeft: 20, marginRight: 20 }}>
                                    <div style={{ width: 100 }}><span className="font-bold-s12-c000000">연락처*</span></div>
                                    <div>
                                        <InputBase sx={{ ml: 1, flex: 1 }}
                                            placeholder="01000000000"
                                            {...register('phone')}
                                            value={profile?.phone || ""}
                                            onChange={onChange}
                                            readOnly={!edit}
                                            inputProps={{ style: { fontSize: 13 } }} // font size of input text
                                        />
                                        {errors.phone && (
                                            <FormErrorMessage errorMessage={errors.phone.message!} />
                                        )}
                                    </div>
                                </div>
                                <div className="sign-line"></div>
                                <div className="common-flex-row-wrap-vcenter" style={{ marginLeft: 20, marginRight: 20 }}>
                                    <div style={{ width: 100 }}><span className="font-bold-s12-c000000">주소</span></div>
                                    <div>
                                        <InputBase sx={{ ml: 1, flex: 1 }}
                                            placeholder="주소입력"
                                            {...register('address')}
                                            value={profile?.address || ""}
                                            onChange={onChange}
                                            readOnly={!edit}
                                            inputProps={{ style: { fontSize: 13 } }} // font size of input text
                                        />
                                    </div>
                                </div>
                                <div className="sign-line"></div>
                                <div className="common-flex-row-wrap-vcenter" style={{ marginLeft: 20, marginRight: 20 }}>
                                    <div style={{ width: 100 }}><span className="font-bold-s12-c000000">상세주소</span></div>
                                    <div>
                                        <InputBase sx={{ ml: 1, flex: 1 }}
                                            placeholder="상세주소입력"
                                            {...register('addressDetail')}
                                            value={profile?.addressDetail || ""}
                                            onChange={onChange}
                                            readOnly={!edit}
                                            inputProps={{ style: { fontSize: 13 } }} // font size of input text
                                        />
                                    </div>
                                </div>
                                <div className="sign-line"></div>
                                <div className="common-flex-row-wrap-vcenter" style={{ marginLeft: 20, marginRight: 20 }}>
                                    <div style={{ width: 100 }}><span className="font-bold-s12-c000000">계좌번호</span></div>
                                    <div>
                                        <InputBase sx={{ ml: 1, flex: 1 }}
                                            placeholder="계좌번호입력"
                                            {...register('account')}
                                            value={profile?.account || ""}
                                            onChange={onChange}
                                            readOnly={!edit}
                                            inputProps={{ style: { fontSize: 13 } }} // font size of input text
                                        />
                                    </div>
                                </div>
                                <div className="sign-line"></div>
                            </div>
                        </div>
                    </div>
                </form>
            </div>
        </>

    )
}

export default MyInfoEditor