import { InputBase } from '@mui/material';
import * as React from 'react';
import { BoxLabel } from '../../../components';
import { CareerListArray, CertificateListArray } from '../../../api/atom.myinfo';
import { careerValidation, certificateValidation } from '../../../api/yup';
import ButtonSquareLineB from '../../../components/ButtonSquareLineB';
import { yupResolver } from '@hookform/resolvers/yup';
import { useForm } from 'react-hook-form';
import { useState } from 'react';
import ChipMedium from '../../../components/ChipMedium';
import DragDropFile from './DragDropFile';
import FormErrorMessage from '../../../components/FormErrorMessage';

interface Props {
    clickCareerAddEvent: () => void;
    item: CareerListArray;
    clickCareerDeleteEvent: (itemId: number) => void;
    clickCareerUpdateEvent: (itemId: number, data: CareerListArray) => void;
}

const CareerLists: React.FC<Props> = (props) => {

    const [edit, setEdit] = useState(false);
    const onEdit = () => {
        setEdit(() => !edit);
    }
    const initInput = {
        id: props.item.id,
        organization: props.item.organization,
        fileName: props.item.fileName,
    }
    const [input, setInput] = useState(initInput);
    const onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const { name, value } = e.target;
        console.log("name", name, "value", value);
        setInput({
            ...input,
            [name]: value
        });
    }
    const { handleSubmit, formState: { errors }, register } = useForm<CareerListArray>({
        resolver: yupResolver(careerValidation),
    });
    const onSubmit = handleSubmit((data: CareerListArray) => {
        console.log("CareerListArray", data);
        props.clickCareerUpdateEvent(props.item.id, input);
    });

    return (
        <>
            <form onSubmit={onSubmit}>
                <div className="media-container-round-white-shadow-padding40" style={{ marginTop: 10 }}>
                    <div className="common-flex-column-wrap">

                        <div className="common-flex-row-wrap-space-between-vcenter" style={{ marginLeft: 10, marginRight: 10 }}>
                            <div className="item-lr">
                                <div className="common-flex-row-wrap-vcenter">
                                    <span className="font-bold-s21-c573300" style={{ marginLeft: 8 }}>경력정보</span>
                                </div>
                            </div>
                            <div className="item-lr">
                                {
                                    edit === false ?
                                        <ButtonSquareLineB type={0} onClick={() => onEdit()}>편집</ButtonSquareLineB>
                                        :
                                        <div className="common-flex-row-wrap-vcenter">
                                            <ButtonSquareLineB type={1} onClick={() => onSubmit()}>저장하기</ButtonSquareLineB>
                                            <ButtonSquareLineB type={1} onClick={() => props.clickCareerAddEvent()}>경력추가</ButtonSquareLineB>
                                            {props.item.id !== 0 &&
                                                <ButtonSquareLineB type={1} onClick={() => props.clickCareerDeleteEvent(props.item.id)}>삭제하기</ButtonSquareLineB>
                                            }
                                            <ButtonSquareLineB type={1} onClick={() => onEdit()}>취소</ButtonSquareLineB>
                                        </div>
                                }
                            </div>
                        </div>
                        <div className="sign-line"></div>


                        <div className="common-flex-row-wrap-space-between-vcenter" style={{ marginLeft: 10, marginRight: 10 }}>
                            <div className="common-flex-row-wrap-vcenter" >
                                <img src="/asset/img/myinfo_img_brown_spot.svg" />
                                <span className="font-bold-s15-c000000" style={{ marginLeft: 10, width: 100 }}>기관</span>
                                <div style={{ flex: 1 }}>
                                    {
                                        edit === false ?
                                            <InputBase sx={{ ml: 1, flex: 1, width: '100%' }}
                                                placeholder="기관"
                                                value={input.organization || ""}
                                                readOnly={true} />
                                            :
                                            <>
                                                <InputBase sx={{ ml: 1, flex: 1, width: '100%' }}
                                                    placeholder="기관"
                                                    {...register('organization')}
                                                    value={input.organization || ""}
                                                    onChange={onChange} />
                                                {errors.organization && (
                                                    <FormErrorMessage errorMessage={errors.organization.message!} />
                                                )}
                                            </>
                                    }

                                </div>
                            </div>
                            <div className="common-flex-row-wrap-space-between-vcenter">
                                <div>
                                </div>
                                <div className="common-flex-row-wrap-vcenter" style={{ marginTop: 10, marginLeft: 10 }}>
                                    {/* <ChipLarge>임시메뉴1</ChipLarge> */}
                                    <ChipMedium>1년 이하</ChipMedium>
                                    <ChipMedium>2년~5년</ChipMedium>
                                    <ChipMedium>5년이상</ChipMedium>
                                    <ChipMedium>10년 이상</ChipMedium>
                                </div>
                            </div>
                        </div>

                        <div className="sign-line"></div>

                        <div className="common-flex-row-wrap-space-between-vcenter" style={{ marginLeft: 10, marginRight: 10 }}>
                            <div className="common-flex-row-wrap-vcenter" >
                                <img src="/asset/img/myinfo_img_brown_spot.svg" />
                                <span className="font-bold-s15-c000000" style={{ marginLeft: 10, width: 100 }}>파일첨부</span>
                                {
                                    edit === false ?
                                        <InputBase sx={{ ml: 1, flex: 1 }}
                                            placeholder="파일이름"
                                            value={input.fileName || ""} readOnly={true} />
                                        :
                                        <>
                                            <DragDropFile />
                                        </>
                                }
                            </div>
                            <div className="common-flex-row-wrap-space-between-vcenter">
                                <div>
                                </div>
                                <div className="common-flex-row-wrap-vcenter" style={{ marginTop: 10, marginLeft: 10 }}>
                                    {
                                        edit === false ?
                                            <BoxLabel background="#FFBF26" marginLeft={10}>인증 대기중</BoxLabel>
                                            :
                                            <>
                                            </>
                                    }
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </form>
        </>
    )
}
export default CareerLists

