import axios, { AxiosResponse } from 'axios';
import { CommonType, GroupInfo, PostType } from './post.interface';
import Cookies from 'universal-cookie';
import { AccountLogin } from './atom.sign';
import { GroupDetail } from './atom.group';

const cookie = new Cookies();
let isTokenRefreshing = false;
let authUrl = "http://jsonplaceholder.typicode.com/";
let groupUrl = "http://ec2-52-79-232-227.ap-northeast-2.compute.amazonaws.com:8080";
let fileUrl = "http://good-beginning-api.koreacentral.cloudapp.azure.com:9090";
const client = axios.create({
    baseURL: '',
    timeout: 15000,
});

client.interceptors.request.use(async (config) => {
    try{
        // 요청을 보내기 전 수행할 작업
       // document.body.classList.add('loading-indicator');
       const ACCESS_TOKEN = cookie.get('ACCESS_TOKEN');
       const REFRESH_TOKEN = cookie.get('refresh-token');
       const XSRF_TOKEN = cookie.get('XSRF-TOKEN');
       client.defaults.headers.common['Authorization'] = `Bearer ${ACCESS_TOKEN}`;
       client.defaults.headers.common['Content-Type'] = 'application/json';
       //client.defaults.headers.common['Content-Type'] = 'multipart/form-data';
       //client.defaults.headers.common['X-XSRF-TOKEN'] = XSRF_TOKEN; 
       console.log("request url", config.url);
    }
    catch(e)
    {
        alert('Error request' + e);
        
    }
    return config;
});

client.interceptors.response.use(
    (response) => {
      console.log("response.status", response.status);
      console.log();
      return response;
    },
    (error) => {
      const {
        config,
        response: { status },
      } = error;  
      const originalRequest = config;  
      console.log("status", status);
      console.log();
      if (status === 401) {
        if (!isTokenRefreshing) {
            //  401은 Access Token or Refresh Token 이 invalid 될때
            //  response data의 code값이 
            //  4401 : access Token error , 4402: refresh Token error            
            if(error.response.data.code === '4401'){
                window.location.href= '/signin'; 
            }
            if(error.response.data.code === '4402'){
                isTokenRefreshing = true;
                const REFRESH_TOKEN = cookie.get('refresh-token');
                axios({
                    method: 'post',
                    url: `${authUrl}/api/auth/refresh`,
                    data: { REFRESH_TOKEN },
                }).then((response) => {
                    const accessToken = response.data.data.accessToken;
                    //const accessToken = `${accessTokens.header}.${accessTokens.payload}.${accessTokens.signature}`;  
                    isTokenRefreshing = false;
                    cookie.set('access_token', accessToken);
                    client.defaults.headers.common['Authorization'] = `Bearer ${accessToken}`;
                    client.defaults.headers.common['Content-Type'] = 'application/json';
                    return axios(originalRequest);
                });
            }           
            
        }
        
      }
      return Promise.reject(error);
    },
  );

const responseBody = (response: AxiosResponse) => response.data;

const requests = {
    get: (url: string) => client.get(url).then(responseBody),
    post: (url: string, body: {}) => client.post(url, body).then(responseBody),
    put: (url: string, body: {}) => client.put(url, body).then(responseBody),
    delete: (url: string) => client.delete(url).then(responseBody),
};

export const Post = {
    loginPost: (post:AccountLogin)
    : Promise<CommonType> => requests.post('/authorization-service/login',post),
    getGroupDetail: (id: number)
    : Promise<CommonType> => requests.get(`/group-service/query/api/v1/groups/${id}`),
    getGroupCards: (searchText:string,therapySubjects:string,page:number,sort:string,size:number)
    : Promise<CommonType> => requests.get(`/group-service/query/api/v1/groups/search?${searchText}&${therapySubjects}&${page}&${sort}&${size}`),
    signUpFile: (post:FormData)
    : Promise<CommonType> => requests.post('/api/user/v1/childrens/160/todos/2829/videos',post),

    getPosts: (): Promise<PostType[]> => requests.get('posts'),
    getAPost: (id: number): Promise<PostType> => requests.get(`posts/${id}`),
    createPost: (post: PostType): Promise<PostType> => requests.post('posts', post),
    updatePost: (post: PostType, id: number): Promise<PostType> => requests.put(`posts/${id}`, post),
    deletePost: (id: number): Promise<void> => requests.delete(`posts/${id}`),
};

