import { Input, InputAdornment, TextField } from "@mui/material";
import { useEffect, useState } from "react";
import { useRecoilState, useSetRecoilState } from "recoil";
import ButtonSquareSmall from "../../components/ButtonSquareSmall";
import ChipMedium from "../../components/ChipMedium";
import { initOrderInfoAtom, initOrderInfoAtom2 } from "../../api/atom.groupadd";

interface localProps {

}
const ariaLabel = { 'aria-label': 'description' };
const OrderInfo: React.FC<localProps> = (props) => {
    const [orderInfoAtom, setOrderInfoAtom] = useRecoilState<number[]>(initOrderInfoAtom);
    const setOrderUseSetRecoilState = useSetRecoilState(initOrderInfoAtom);

    // --- menu memory btn
    const [menu, setMenu] = useState(1);
    const menuStr = ["", "언어치료", "인지학습", "놀이", "물리/운동", "작업", "감각통합", "ABA", "음악", "미술", "모니터링", "심리/발달", "사회성", "짝치료", "특수체육", "기타"];
    const [menuArray, setMenuArray] = useState(orderInfoAtom);
    const changeMenu = (num: number) => {
        setMenu(() => num);
        const newArr = [...menuArray];
        menuArray.map((item: number, index: number) => {
            if (num == index) {
                if (item == 0) { newArr[index] = 1 }
                if (item == 1) { newArr[index] = 0 }
            }
            setMenuArray(() => newArr);
            //setStep4BtnUseSetRecoilState(() => newArr);
        });
    }
    const clickMenu = (num: number) => {
        changeMenu(num);
    };
    let strArray: { subject: string }[] = [];
    // menu memory btn ---

    const [inputText, setInputText] = useState("");
    useEffect(() => {
        //console.log("menuArray", menuArray);
        strArray = [];
        menuArray.map((item: number, index: number) => {
            if (item == 1) {
                strArray.push({ "subject": menuStr[index] });
            }
        });

        //console.log("array", strArray);
        // const newValue = { group_therapy_subject_response: { etc: inputText, therapy_subject_responses: strArray } }
        // setStepUseSetRecoilState(() => ({ ...groupStepAtom, ...newValue }));
        setOrderUseSetRecoilState(() => menuArray);
    }, [menuArray, inputText]);
    const onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        setInputText(() => e.target.value);
        //setErrorMessage("");
    }

    const [type, setType] = useState([0, 0]);
    const onChangeType = (value: number) => {
        if (value == 0) { setType([1, 0]) }
        if (value == 1) { setType([0, 1]) }
    }

    return (
        <>
            <div className="media-container-round-grey-shadow-padding40" style={{ marginTop: 10 }}>

                <div className="media-container-round-white-shadow-padding40" style={{ marginTop: 10 }}>
                    {/* 내그룹 - title */}
                    <div className="common-flex-row-wrap-space-between-vcenter" style={{ marginLeft: 10, marginRight: 10 }}>
                        <div className="item-lr">
                            <div className="common-flex-row-wrap-vcenter">
                                <img src="/asset/img/myinfo_custominfo_icon_medkit.svg" />
                                <span className="font-bold-s21-c573300" style={{ marginLeft: 8 }}>치료과목</span>
                                <span className="font-normal-s12-c573300" style={{ marginLeft: 8 }}>*중복선택 가능</span>
                            </div>
                        </div>
                        <div className="item-lr">
                            <ButtonSquareSmall>저장</ButtonSquareSmall>
                        </div>
                    </div>
                    <div className="sign-line"></div>
                    <div className="common-flex-row-wrap-vcenter" style={{ marginTop: 10 }}>
                        <ChipMedium onClick={() => clickMenu(1)} type={menuArray[1]}>언어치료</ChipMedium>
                        <ChipMedium onClick={() => clickMenu(2)} type={menuArray[2]}>인지학습</ChipMedium>
                        <ChipMedium onClick={() => clickMenu(3)} type={menuArray[3]}>놀이</ChipMedium>
                        <ChipMedium onClick={() => clickMenu(4)} type={menuArray[4]}>물리/운동</ChipMedium>
                        <ChipMedium onClick={() => clickMenu(5)} type={menuArray[5]}>작업</ChipMedium>
                        <ChipMedium onClick={() => clickMenu(6)} type={menuArray[6]}>감각통합</ChipMedium>
                        <ChipMedium onClick={() => clickMenu(7)} type={menuArray[7]}>ABA</ChipMedium>
                        <ChipMedium onClick={() => clickMenu(8)} type={menuArray[8]}>음악</ChipMedium>
                        <ChipMedium onClick={() => clickMenu(9)} type={menuArray[9]}>미술</ChipMedium>
                        <ChipMedium onClick={() => clickMenu(10)} type={menuArray[10]}>모니터링</ChipMedium>
                        <ChipMedium onClick={() => clickMenu(11)} type={menuArray[11]}>심리/발달</ChipMedium>
                        <ChipMedium onClick={() => clickMenu(12)} type={menuArray[12]}>사회성</ChipMedium>
                        <ChipMedium onClick={() => clickMenu(13)} type={menuArray[13]}>짝치료</ChipMedium>
                        <ChipMedium onClick={() => clickMenu(14)} type={menuArray[14]}>특수체육</ChipMedium>
                        <ChipMedium onClick={() => clickMenu(15)} type={menuArray[15]}>기타</ChipMedium>
                    </div>
                    <div className="common-flex-row-wrap-hcenter-vcenter" style={{ marginTop: 0 }}>

                        <TextField id="standard-basic" style={{ width: '95%' }}
                            label="원하는 치료 과목을 입력해주세요."
                            variant="standard"
                            value={inputText}
                            onChange={onChange}
                            InputProps={{
                                startAdornment: (
                                    <InputAdornment position="start">
                                        <img src="/asset/img/treatment_icon_pencil.svg" />
                                    </InputAdornment>
                                )
                            }} />
                    </div>
                </div>

                <div className="media-container-round-white-shadow-padding40" style={{ marginTop: 10 }}>
                    {/* 내그룹 - title */}
                    <div className="common-flex-row-wrap-space-between-vcenter" style={{ marginLeft: 10, marginRight: 10 }}>
                        <div className="item-lr">
                            <div className="common-flex-row-wrap-vcenter">
                                <img src="/asset/img/group_detail_icon_circle.svg" />
                                <span className="font-bold-s21-c573300" style={{ marginLeft: 8 }}>선호도</span>
                            </div>
                        </div>
                        <div className="item-lr">
                            <ButtonSquareSmall>저장</ButtonSquareSmall>
                        </div>
                    </div>
                    <div className="sign-line"></div>
                    <div className="common-flex-row-wrap-vcenter" style={{ marginTop: 10 }}>
                        <ChipMedium onClick={() => onChangeType(0)} type={type[0]}>개별치료</ChipMedium>
                        <ChipMedium onClick={() => onChangeType(1)} type={type[1]}>그룹치료</ChipMedium>
                        <span className="font-bold-s12-c000000" style={{ marginLeft: 8 }}>주</span>
                        <div style={{ width: 20, marginLeft: 8 }}><Input defaultValue="0" inputProps={ariaLabel} style={{ textAlign: "right" }} /></div>
                        <span className="font-bold-s12-c000000" style={{ marginLeft: 8 }}> 회, 1회</span>
                        <div style={{ width: 20, marginLeft: 8 }}><Input defaultValue="0" inputProps={ariaLabel} style={{ textAlign: "right" }} /></div>
                        <span className="font-bold-s12-c000000" style={{ marginLeft: 8 }}> 시간 진행희망</span>
                    </div>

                </div>

            </div>
        </>

    )

}
export default OrderInfo


