import { useEffect, useRef, useState } from "react";
import { ChildInfoListArray, initChildInfoItem } from "../../api/atom.myinfo";
import ChildInfoLists from "./components/ChildInfoList";

interface localProps {
}

const ChildInfo: React.FC<localProps> = (props) => {

    // ChildInfoList
    const [childInfoList, setChildInfoList] = useState<ChildInfoListArray[]>([initChildInfoItem]);
    const nextChildInfoId = useRef(0);
    const clickAddEvent = () => {
        const id = nextChildInfoId.current += 1;
        const list = {
            id: nextChildInfoId.current,
            name: '',
            gender: '',
            birth: '',
            memo: '',
        };
        setChildInfoList(() => childInfoList.concat(list));
    };
    const clickUpdateEvent = (itemId: number, data: ChildInfoListArray) => {
        setChildInfoList(
            childInfoList.map(
                info => info.id === itemId
                    ? { ...info, ...data }
                    : info
            ));
    };
    const clickDeleteEvent = (itemId: number) => {
        setChildInfoList(childInfoList.filter(list => list.id !== itemId));
    };
    useEffect(() => {
        console.log("childInfoList", childInfoList);
    }, [childInfoList])

    return (
        <>
            <div className="media-container-round-grey-shadow-padding40" style={{ marginTop: 10 }}>
                {
                    localStorage.getItem('therapist') === "true" &&
                    <div style={{ marginTop: 10 }}>
                        {childInfoList.map((item, index) => (
                            <ChildInfoLists
                                key={item.id}
                                item={item}
                                clickAddEvent={clickAddEvent}
                                clickDeleteEvent={clickDeleteEvent}
                                clickUpdateEvent={clickUpdateEvent}
                            />
                        ))}
                    </div>
                }
            </div>
            <div className="common-flex-row-reverse-wrap-vcenter" style={{ marginTop: 20 }}>
                <img src="/asset/img/mypage_child_info_img_child_plus_button.svg"
                    onClick={() => clickAddEvent()}
                    style={{ cursor: 'pointer' }} />
            </div>

        </>

    )

}
export default ChildInfo


