import { TextField, Checkbox } from "@mui/material"
import { useEffect, useState } from "react"
import { useRecoilState, useSetRecoilState } from "recoil"
import { ListViewTitle, BoxLabel } from "../../components"
import ButtonSquare from "../../components/ButtonSquare"
import ButtonSquareSmall from "../../components/ButtonSquareSmall"
import GroupAddTitle from "./components/GroupAddTitle"
import SliderMultiAge from "../../components/SliderMultiAge"
import { GroupAddStep, initGroupAddStepAtom, sliderAge } from "../../api/atom.groupadd"


interface Step6Props {
    allEdit: boolean;
    linkGroupAddStep: (num: number) => void;
}

const Step6: React.FC<Step6Props> = (props) => {
    const [groupStepAtom, setGroupStepAtom] = useRecoilState<GroupAddStep>(initGroupAddStepAtom);
    const setStepUseSetRecoilState = useSetRecoilState(initGroupAddStepAtom);
    const [sliderAgeAtom, setSliderAgeAtom] = useRecoilState(sliderAge);
    useEffect(() => {
        const newValue1 = { min_children_count: sliderAgeAtom.min_children_count }
        const newValue2 = { max_children_count: sliderAgeAtom.max_children_count }
        setStepUseSetRecoilState(() => ({ ...groupStepAtom, ...newValue1, ...newValue2 }));
    }, [sliderAgeAtom])
    // 체크박스 
    const therapistsList = [
        { id: 0, text: 'therapists 0' },
        { id: 1, text: 'therapists 1' },
        { id: 2, text: 'therapists 2' },
        { id: 3, text: 'therapists 3' },
        { id: 4, text: 'therapists 3' },
        { id: 5, text: 'therapists 3' },
        { id: 6, text: 'therapists 3' }
    ]
    const [posts, setPosts] = useState(therapistsList);
    const [checkedItems, setCheckedItems] = useState(new Set());
    const [checkedState, setCheckedState] = useState(
        new Array(posts.length).fill(false)
    );
    const checkEvent = (itemId: number) => (e: React.ChangeEvent<HTMLInputElement>) => {
        console.log("checkEvent itemId", itemId, e.target.checked);
        checkedItemHandler(itemId, e.target.checked);
        const updatedCheckedState = checkedState.map((item, index) =>
            index === itemId ? !item : item
        );
        setCheckedState(updatedCheckedState);
    }
    const checkedItemHandler = (itemId: number, isChecked: boolean) => {
        if (isChecked) {
            checkedItems.add(itemId);
            setCheckedItems(() => checkedItems);
        } else if (!isChecked && checkedItems.has(itemId)) {
            checkedItems.delete(itemId);
            setCheckedItems(() => checkedItems);
        }
        console.log("checkedItemHandler checkedItems", checkedItems);
    };
    const allCheckedHandler = (isChecked: boolean) => {
        if (isChecked) {
            setCheckedItems(new Set(posts.map(({ id }) => id)));
        } else {
            checkedItems.clear();
            setCheckedItems(new Set());
            setCheckedState(new Array(posts.length).fill(false));
            console.log("checkedItemHandler checkedItems", checkedItems);
        }
    };


    return (
        <>
            {/* 치료구분 - title */}
            <div className="common-flex-row-wrap-space-between-vcenter"
                style={{ marginTop: 20 }}>
                <div className="item-lr">
                    <div className="common-flex-row-wrap-vcenter">
                        <span className="font-bold-s21-c573300"><span className="span-half-FFE197">구성원</span></span>
                    </div>
                </div>
                <div className="item-lr">
                    <div className="common-flex-row-wrap-vcenter">
                        {
                            props.allEdit === true &&
                            <ButtonSquareSmall background="#FF9100">저장</ButtonSquareSmall>
                        }
                        <ButtonSquareSmall background="#FF9100" onClick={() => props.linkGroupAddStep(5)}>이전</ButtonSquareSmall>
                        <ButtonSquareSmall background="#FF9100" onClick={() => props.linkGroupAddStep(7)}>다음</ButtonSquareSmall>
                    </div>
                </div>
            </div>

            <GroupAddTitle background="#FFB9A1" image="/asset/img/new_member.svg">
                개별치료를 선택한 경우 구성원(보호자) 1명만 선택할 수 있습니다.<br />
                그룹치료를 선택한 경우 다수의 구성원(치료사, 보호자)에게 그룹초대를 보낼 수
                있습니다. <br />
                그룹초대는 그룹 생성 후에 시작 일시전까지 자유롭게 진행할 수 있습니다. <br />
                별도의 초대를 하지않은 경우, 자유롭게 참여신청을 보내게 됩니다. <br />
                대표 치료사가 참여 신청 확인 후 승인/거절을 통해 그룹을 구성할 수 있습니다.<br />
            </GroupAddTitle>


            <div className="media-group-grey-container" style={{ marginTop: 20 }}>
                <div>
                    <ListViewTitle icon="/asset/img/new_target_02.svg">구성인원</ListViewTitle>
                </div>
                <div className="group-slider-container" style={{ marginTop: 20 }}>
                    <SliderMultiAge></SliderMultiAge>
                    <div style={{ marginTop: 10 }}>
                        {sliderAgeAtom.min_children_count + "명 ~ " + sliderAgeAtom.max_children_count + "명"}
                    </div>
                </div>
            </div>

            <div className="media-group-grey-container" style={{ marginTop: 20, marginBottom: 20 }}>
                <div>
                    <ListViewTitle icon="/asset/img/new_target_02.svg">치료사검색</ListViewTitle>
                </div>
                <div className="common-flex-row-wrap-hcenter-vcenter" style={{ marginTop: 20 }}>
                    <div className="search">
                        <div className="search-input">
                            <TextField
                                style={{ width: '100%' }}
                                name="text"
                                size="small"
                                id="outlined-error"
                                placeholder="회원이름 또는 검색어를 입력해주세요."
                                label=""
                                variant="standard"
                                InputProps={{
                                    disableUnderline: true,
                                }}
                            />
                        </div>
                        <div className="search-button"><img src="/asset/img/treatment_icon_search.svg" /></div>
                    </div>
                </div>

                <div className='group-white-container'
                    style={{ padding: 10, marginTop: 20 }}>
                    <div className="common-flex-column-wrap">
                        {
                            posts.map((item, index) => (
                                <div key={index} className="group-card-title">
                                    <Checkbox key={index}
                                        checked={checkedState[index]}
                                        onChange={checkEvent(item.id)}
                                        size="small" />
                                    <img src="/asset/img/main_img_teacher.svg" />
                                    <BoxLabel background="#000000" triangle={true} marginLeft={5}>홍길동 선생님 (therapist@gmail.com)</BoxLabel>
                                    <BoxLabel background="#FF9100" marginLeft={10}>굿로드 인증</BoxLabel>
                                    <BoxLabel background="#00CDB6" marginLeft={10}>10년 이상</BoxLabel>
                                </div>
                            ))
                        }

                    </div>
                </div>

                <div className="media-grid--col2__11" style={{ marginTop: 20 }}>
                    <div className="box-cgc">
                        <ButtonSquare onClick={() => allCheckedHandler(false)}>선택초기화</ButtonSquare>
                    </div>
                    <div className="box-cgc">
                        <ButtonSquare>회원선택</ButtonSquare>
                    </div>
                </div>
            </div>
            <div className="media-blank-bottom"></div>
        </>

    )

}
export default Step6


