import { ContactPageSharp } from "@mui/icons-material"
import { FormControlLabel, InputAdornment, Radio, RadioGroup, RadioProps, styled, TextField } from "@mui/material"
import { truncate } from "fs"
import JoditEditor from "jodit-react"
import { useEffect, useRef, useState } from "react"
import { useRecoilState, useSetRecoilState } from "recoil"
import { ListViewTitle } from "../../components"
import ButtonSquareSmall from "../../components/ButtonSquareSmall"
import GroupAddTitle from "./components/GroupAddTitle"
import { GroupAddStep, initGroupAddStepAtom } from "../../api/atom.groupadd"

interface Step9Props {
    allEdit: boolean;
    linkGroupAddStep: (num: number) => void;
}

const BpIcon = styled('span')(({ theme }) => ({
    borderRadius: '50%',
    width: 24,
    height: 24,
    boxShadow:
        theme.palette.mode === 'dark'
            ? '0 0 0 1px rgb(16 22 26 / 40%)'
            : 'inset 0 0 0 1px rgba(16,22,26,.2), inset 0 -1px 0 rgba(16,22,26,.1)',
    backgroundColor: theme.palette.mode === 'dark' ? '#394b59' : '#f5f8fa',
    backgroundImage:
        theme.palette.mode === 'dark'
            ? 'linear-gradient(180deg,hsla(0,0%,100%,.05),hsla(0,0%,100%,0))'
            : 'linear-gradient(180deg,hsla(0,0%,100%,.8),hsla(0,0%,100%,0))',
    '.Mui-focusVisible &': {
        outline: '2px auto rgba(19,124,189,.6)',
        outlineOffset: 2,
    },
    'input:hover ~ &': {
        backgroundColor: theme.palette.mode === 'dark' ? '#30404d' : '#ebf1f5',
    },
    'input:disabled ~ &': {
        boxShadow: 'none',
        background:
            theme.palette.mode === 'dark' ? 'rgba(57,75,89,.5)' : 'rgba(206,217,224,.5)',
    },
}));

const BpCheckedIcon = styled(BpIcon)({
    backgroundColor: '#FF9100',
    backgroundImage: 'linear-gradient(180deg,hsla(0,0%,100%,.1),hsla(0,0%,100%,0))',
    '&:before': {
        display: 'block',
        width: 24,
        height: 24,
        backgroundImage: 'radial-gradient(#fff,#fff 28%,transparent 32%)',
        content: '""',
    },
    'input:hover ~ &': {
        backgroundColor: '#FF9100',
    },
});

// Inspired by blueprintjs
function BpRadio(props: RadioProps) {
    return (
        <Radio
            sx={{
                '&:hover': {
                    bgcolor: 'transparent',
                },
            }}
            disableRipple
            color="default"
            checkedIcon={<BpCheckedIcon />}
            icon={<BpIcon />}
            {...props}
        />
    );
}


const Step9: React.FC<Step9Props> = (props) => {
    const [groupStepAtom, setGroupStepAtom] = useRecoilState<GroupAddStep>(initGroupAddStepAtom);
    const setStepUseSetRecoilState = useSetRecoilState(initGroupAddStepAtom);

    const [inputText1, setInputText1] = useState(groupStepAtom.group_detail_info.title);
    const onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        console.log("inputText1", e.target.value);
        setInputText1(() => e.target.value);
    }
    const [inputText2, setInputText2] = useState(groupStepAtom.group_detail_info.answer_about_time);
    const onChange2 = (e: React.ChangeEvent<HTMLInputElement>) => {
        setInputText2(() => e.target.value);
    }
    const [inputText3, setInputText3] = useState(groupStepAtom.group_detail_info.answer_about_region);
    const onChange3 = (e: React.ChangeEvent<HTMLInputElement>) => {
        setInputText3(() => e.target.value);
    }

    const editor = useRef(null)
    const [content, setContent] = useState(groupStepAtom.group_detail_info.contents);

    const config = {
        readonly: false, // all options from https://xdsoft.net/jodit/doc/
        height: 500
    }
    const [radio, setRadio] = useState(groupStepAtom.representation_image);
    const [radioChecked, setRadioChecked] = useState([false, false, false, false, false, false]);
    const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        setRadio(() => e.target.value);
    };

    useEffect(() => {
        console.log(groupStepAtom);
        if (groupStepAtom.representation_image == "new_group_detail_img_1.svg") { setRadioChecked([true, false, false, false, false, false]) }
        if (groupStepAtom.representation_image == "new_group_detail_img_2.svg") { setRadioChecked([false, true, false, false, false, false]) }
        if (groupStepAtom.representation_image == "new_group_detail_img_3.svg") { setRadioChecked([false, false, true, false, false, false]) }
        if (groupStepAtom.representation_image == "new_group_detail_img_4.svg") { setRadioChecked([false, false, false, true, false, false]) }
        if (groupStepAtom.representation_image == "new_group_detail_img_5.svg") { setRadioChecked([false, false, false, false, true, false]) }
        if (groupStepAtom.representation_image == "new_group_detail_img_6.svg") { setRadioChecked([false, false, false, false, false, true]) }
    }, [groupStepAtom])

    useEffect(() => {
        console.log("inputText1 >>>", inputText1);
        const inputs = {
            group_detail_info: { title: inputText1, answer_about_time: inputText2, answer_about_region: inputText3, contents: content }
        }
        const newRadio = { representation_image: radio }
        setStepUseSetRecoilState(() => ({ ...groupStepAtom, ...inputs, ...newRadio }));
    }, [radio, inputText1, inputText2, inputText3, content])



    return (
        <>
            {/* 치료구분 - title */}
            <div className="common-flex-row-wrap-space-between-vcenter"
                style={{ marginTop: 20 }}>
                <div className="item-lr">
                    <div className="common-flex-row-wrap-vcenter">
                        <span className="font-bold-s21-c573300"><span className="span-half-FFE197">상세정보</span></span>
                    </div>
                </div>
                <div className="item-lr">
                    <div className="common-flex-row-wrap-vcenter">
                        {
                            props.allEdit === true &&
                            <ButtonSquareSmall background="#FF9100">저장</ButtonSquareSmall>
                        }
                        <ButtonSquareSmall background="#FF9100" onClick={() => props.linkGroupAddStep(8)}>이전</ButtonSquareSmall>
                        <ButtonSquareSmall background="#00CDB6" onClick={() => props.linkGroupAddStep(1)}>완료</ButtonSquareSmall>
                    </div>
                </div>
            </div>

            <GroupAddTitle background="#E1B8F4" image="/asset/img/new_detail_01.svg">
                메인페이지 및 그룹찾기에 노출할 제목과 내용을 자유롭게 입력해주세요.<br />
            </GroupAddTitle>

            <div className="media-group-grey-container" style={{ marginTop: 20 }}>
                <div>
                    <ListViewTitle icon="/asset/img/new_target_02.svg">제목</ListViewTitle>
                </div>
                <div>
                    <TextField id="standard-basic" style={{ width: '100%', marginTop: 20 }}
                        placeholder="그룹 목록에 노출할 제목을 입력해주세요."
                        variant="standard"
                        value={inputText1}
                        onChange={onChange}
                        InputProps={{
                            startAdornment: (
                                <InputAdornment position="start">
                                    <img src="/asset/img/treatment_icon_pencil.svg" />
                                </InputAdornment>
                            )
                        }} />
                </div>
            </div>
            <RadioGroup defaultValue="female" aria-label="gender" name="customized-radios">
                {radioChecked[0]}
                <div className="media-group-grey-container" style={{ marginTop: 20 }}>
                    <div>
                        <ListViewTitle icon="/asset/img/new_target_02.svg">대표이미지 선택</ListViewTitle>
                    </div>
                    <div className="common-row-wrap-hcenter-vstretch" style={{ marginTop: 20 }}>
                        <div className="radio-size bg-02" style={{ width: 294, height: 160, margin: 3 }}>
                            <FormControlLabel value="new_group_detail_img_1.svg" control={<BpRadio checked={radioChecked[0]} onChange={handleChange} />} label="" style={{ marginLeft: 1 }} />
                        </div>
                        <div className="radio-size bg-03" style={{ width: 294, height: 160, margin: 3 }}>
                            <FormControlLabel value="new_group_detail_img_2.svg" control={<BpRadio checked={radioChecked[1]} onChange={handleChange} />} label="" style={{ marginLeft: 1 }} />
                        </div>
                        <div className="radio-size bg-04" style={{ width: 294, height: 160, margin: 3 }}>
                            <FormControlLabel value="new_group_detail_img_3.svg" control={<BpRadio checked={radioChecked[2]} onChange={handleChange} />} label="" style={{ marginLeft: 1 }} />
                        </div>
                    </div>
                    <div className="common-row-wrap-hcenter-vstretch" style={{ marginTop: 20 }}>
                        <div className="radio-size bg-05" style={{ width: 294, height: 160, margin: 3 }}>
                            <FormControlLabel value="new_group_detail_img_4.svg" control={<BpRadio checked={radioChecked[3]} onChange={handleChange} />} label="" style={{ marginLeft: 1 }} />
                        </div>
                        <div className="radio-size bg-06" style={{ width: 294, height: 160, margin: 3 }}>
                            <FormControlLabel value="new_group_detail_img_5.svg" control={<BpRadio checked={radioChecked[4]} onChange={handleChange} />} label="" style={{ marginLeft: 1 }} />
                        </div>
                        <div className="radio-size bg-07" style={{ width: 294, height: 160, margin: 3 }}>
                            <FormControlLabel value="new_group_detail_img_6.svg" control={<BpRadio checked={radioChecked[5]} onChange={handleChange} />} label="" style={{ marginLeft: 1 }} />
                        </div>
                    </div>

                </div>
            </RadioGroup>

            <div className="media-group-grey-container" style={{ marginTop: 20 }}>
                <div>
                    <ListViewTitle icon="/asset/img/new_target_02.svg">내용</ListViewTitle>
                </div>
                <div style={{ marginTop: 20 }}>
                    <div className="common-flex-row-wrap-vcenter">
                        <img src="/asset/img/new_detail_icon_location.svg" style={{ marginRight: 10 }} />
                        <span className="font-bold-s18-c573300">Q. 주로 어느 지역에서 진행되나요?</span>
                    </div>

                    <TextField id="standard-basic" style={{ width: '100%', paddingTop: 10, paddingBottom: 10 }}
                        value={inputText2}
                        onChange={onChange2}
                        label=""
                        placeholder="서울특별시 강동구 인근에서 진행됩니다. 상세한 위치는 매주 변경될 수 있으며, 별도로 안내드리도록 하겠습니다."
                        variant="standard" />
                    <div className="common-flex-row-wrap-vcenter">
                        <img src="/asset/img/new_detail_icon_clock.svg" style={{ marginRight: 10 }} />
                        <span className="font-bold-s18-c573300">Q. 주로 어느 시간대에 진행되나요?</span>
                    </div>
                    <TextField id="standard-basic" style={{ width: '100%', paddingTop: 10, paddingBottom: 10 }}
                        label=""
                        value={inputText3}
                        onChange={onChange3}
                        placeholder="주로 오전 중에 진행됩니다. 또는 00:00 AM ~ 00:00PM 시간대에 진행됩니다."
                        variant="standard" />
                </div>
            </div>
            <div className="group-line" style={{ marginTop: 30 }}></div>
            <div className="common-flex-column-wrap" style={{ marginTop: 30 }}>
                <JoditEditor
                    ref={editor}
                    value={content}
                    config={config}
                    // tabIndex={1} 
                    onBlur={newContent => setContent(newContent)}
                    onChange={newContent => { }}
                />
            </div>
            <div className="media-blank-bottom"></div>
        </>

    )

}
export default Step9



