import { Navigate, Route, RouteProps, useLocation, Outlet } from 'react-router';
import Cookies from 'universal-cookie';

export type ProtectedRouteProps = {
    redirectPath: string;
} & RouteProps;

export default function ProtectedRoute({ redirectPath, ...routeProps }: ProtectedRouteProps) {
    const currentLocation = useLocation();
    const cookie = new Cookies();
    let isAuth = cookie.get("isAuthenticated");

    if (isAuth == "true" && redirectPath === currentLocation.pathname) {
        console.log("Outlet", redirectPath);
        return <Outlet {...routeProps} />;
    } else {
        console.log("Navigate", redirectPath);
        if (isAuth) {
            return <Outlet {...routeProps} />;
        } else {
            return <Navigate to={{ pathname: "/signin" }} />;
        }
        // return <Navigate to={{ pathname: isAuth === "true" ? redirectPath : "/signin" }} />;
    }
};