// 네모버튼 본인인증 신규그룹 
import React from "react";
import styled from "styled-components";

interface ButtonSquareProps {
  children?: React.ReactNode;
  width?: number;
  background?: string;
  type?: number;
  onClick?: () => void;
}

const ButtonSquareLarge: React.FC<ButtonSquareProps> = (props) => {
  const { width, children, background, type, onClick } = props;

  const Styled0 = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2px 10px 2px 10px;
    margin: 15px 0px 15px 0px ;
    height:46px;
    min-width:72px;
   
    font-family: "Elice Digital Baeum",sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 19px;
    text-align: center;
    letter-spacing: 0.03em;
    color: #FFFFFF;

    border-radius: 8px;

    &:active,
    &:hover{
      background: #573300;
      color: #dddddd;
      cursor: pointer;
    }
    &:focus {
      background: var(--button-hover-bg-color, #FFFFFF);
      cursor: pointer;
    }

    &:disabled {
      cursor: default;
      opacity: 0.5;
      background: var(--button-bg-color, #FFFFFF);
    }
  `;

  const Styled1 = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2px 10px 2px 10px;
    margin: 15px 0px 15px 0px ;
    height:46px;
    min-width:72px;
   
    font-family: "Elice Digital Baeum",sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 19px;
    text-align: center;
    letter-spacing: 0.03em;
    color: #573300;
    border: 1px solid #573300;

    border-radius: 8px;

    &:active,
    &:hover{
      background: #573300;
      color: #dddddd;
      cursor: pointer;
    }
    &:focus {
      background: var(--button-hover-bg-color, #FFFFFF);
      cursor: pointer;
    }

    &:disabled {
      cursor: default;
      opacity: 0.5;
      background: var(--button-bg-color, #FFFFFF);
    }
  `;

  return (
    <>
      {
        type === 0 ?
          <Styled0
            style={{
              width: `${width}px`,
              background: `${background}`,
            }}
            onClick={onClick}
          >
            {children}
          </Styled0>
          :
          <Styled1
            style={{
              width: `${width}px`,
              background: `#ffffff`,
            }}
            onClick={onClick}
          >
            {children}
          </Styled1>

      }


    </>

  );
};

ButtonSquareLarge.defaultProps = {
  background: "#573300",
  type: 0
}

export default ButtonSquareLarge

