import * as React from 'react';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import Checkbox from '@mui/material/Checkbox';
import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';

import { styled } from '@mui/material/styles';
import {addWeeks} from 'date-fns';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import DateRangePicker, { DateRange } from '@mui/lab/DateRangePicker';
import DateTimePicker from '@mui/lab/DateTimePicker';
import DesktopTimePicker from '@mui/lab/DesktopTimePicker';
import StaticDatePicker from '@mui/lab/StaticDatePicker';
import moment from 'moment';
import 'moment/locale/ko'; // KTS
import { useState } from 'react';
import ko from 'date-fns/locale/ko';
import LinearProgress, { linearProgressClasses } from '@mui/material/LinearProgress';


interface MainProps {
    
  }

  function getWeeksAfter(date: Date | null, amount: number) {
    return date ? addWeeks(date, amount) : undefined;
  }

  const BorderLinearProgress = styled(LinearProgress)(({ theme }) => ({
    height: 10,
    borderRadius: 5,
    [`&.${linearProgressClasses.colorPrimary}`]: {
      backgroundColor: theme.palette.grey[theme.palette.mode === 'light' ? 200 : 800],
    },
    [`& .${linearProgressClasses.bar}`]: {
      borderRadius: 5,
      backgroundColor: theme.palette.mode === 'light' ? '#1a90ff' : '#308fe8',
    },
  }));



const Main: React.FC<MainProps> = (props) => {

    moment.locale('ko');

    {/* 날짜기간입력 */}
    const [value, setValue] = useState<DateRange<Date>>([null, null]);
    {/* 시간입력 */}
    const [valuet, setValuet] = useState<Date | null>(
      new Date('2018-01-01T00:00:00.000Z'),
    );
    {/* 달력 */}
    const [values, setValues] = useState<Date | null>(new Date());
    const [calendar,setCalendar] = useState("2021-10-28");
    
    {/* 날짜시간입력 */}
    const [valuedt, setValuedt] = useState<Date | null>(new Date());

    const handleDateChange = (date: Date | null) => {
      moment.locale('ko');
      setValues(date);
      const tmp = moment(values).format('YYYY-MM-DD');
      setCalendar(tmp);
      console.log("values", tmp);
    };

    return (
        <div>
            <Box
            component="form"
            sx={{
                '& > :not(style)': { m: 1, width: '25ch' },
            }}
            noValidate
            autoComplete="off"
            >
            {/* 텍스트입력 */}
            {calendar}
            <TextField id="standard-basic" label="Standard" variant="standard" />
            {/* 채크박스 */}

            <FormGroup>
                <FormControlLabel control={<Checkbox defaultChecked />} label="Label" />
            </FormGroup>

            {/* 날짜기간입력 */}
            <LocalizationProvider locale={ko}  dateAdapter={AdapterDateFns}>
                <DateRangePicker
                    disablePast
                    value={value}
                    // maxDate={getWeeksAfter(value[0], 4)}
                    onChange={(newValue) => {
                    setValue(newValue);
                    }}
                    renderInput={(startProps, endProps) => (
                    <React.Fragment>
                        <TextField {...startProps} />
                        <Box sx={{ mx: 2 }}> to </Box>
                        <TextField {...endProps} />
                    </React.Fragment>
                    )}
                />
                {/* 시간입력 */}
                <DesktopTimePicker
                        label="For desktop"
                        value={valuet}
                        onChange={(newValue) => {
                            setValuet(newValue);
                        }}
                        renderInput={(params) => <TextField {...params} />}
                        />
                </LocalizationProvider>

                {/* 달력 */}
                <LocalizationProvider locale={ko} dateAdapter={AdapterDateFns}>
                <StaticDatePicker
                    displayStaticWrapperAs="desktop"
                    openTo="day"
                    // renderDay={renderWeekPickerDay}
                    
                    value={values}
                    // onChange={(newValue) => {
                    // setValues(newValue);
                    // }}
                    onChange={(date) =>handleDateChange(date)}
                    renderInput={(params) => <TextField  {...params} />}
                />
               
                  <DateTimePicker
                    renderInput={(props) => <TextField {...props} />}
                    label="DateTimePicker"
                    value={valuedt}
                    onChange={(newValue) => {
                      setValuedt(newValue);
                    }}
                  />
                </LocalizationProvider>


                {/* 로딩바 */}
                <Box sx={{ flexGrow: 1 }}>
                {values?.getDate}
                <br />
                <BorderLinearProgress variant="determinate" value={50} />
                </Box>
                
            </Box>

        </div>
    )
}

export default Main
