import * as React from "react";
import PropTypes from "prop-types";
import Slider, { SliderThumb } from "@mui/material/Slider";
import { styled } from '@mui/material/styles';
import clsx from "clsx";
import { useEffect } from "react";
import { useRecoilState, useSetRecoilState } from "recoil";
import { sliderBasic, sliderYear } from "../api/atom.groupadd";

const AirbnbSlider = styled(Slider)(({ theme }) => ({
    color: "#573300",
    height: 6,
    padding: "13px 0",
    "& .MuiSlider-thumb": {
        height: 30,
        width: 34,
        backgroundColor: "#ECECEC",
        border: "0px solid currentColor",
        "&.first-thumb": {
            border: "0px dashed red"
        },
        "&.second-thumb": {
            border: "0px dashed purple"
        },
        "&:hover": {
            boxShadow: "0 0 0 0px rgba(58, 133, 137, 0.16)"
        },
        "& .airbnb-bar": {
            height: 9,
            width: 1,
            marginLeft: 1,
            marginRight: 1
        },
        "&.first-thumb .airbnb-bar": {
            backgroundColor: "red"
        },
        "&.second-thumb .airbnb-bar": {
            backgroundColor: "currentColor"
        },
        '&:before': {
            display: 'none',
        }
    },
    "& .MuiSlider-track": {
        border: 'none',
        height: 6
    },
    "& .MuiSlider-rail": {
        color: "#ffffff",
        // color: theme.palette.mode === "dark" ? "#ffffff" : "#ffffff",
        // opacity: theme.palette.mode === "dark" ? undefined : 1,
        height: 6
    }
}));
const marks = [
    { value: 1, label: '1회', },
    { value: 2, label: '2회', },
    { value: 3, label: '3회', },
    { value: 4, label: '4회', },
    { value: 5, label: '5회', },
    { value: 6, label: '6회', },
    { value: 7, label: '7회', },
];
function valuetext(value: number) {
    return `${value}세`;
}
interface AirbnbThumbComponentProps extends React.HTMLAttributes<unknown> { }

function AirbnbThumbComponent(props: AirbnbThumbComponentProps) {
    const { children, className, ...other } = props;
    const extraClassName = "first-thumb";
    return (
        <SliderThumb {...other} className={clsx(className, extraClassName)}>
            {children}
            <span className="rc-slider-handle-l" />
        </SliderThumb>
    );
}

AirbnbThumbComponent.propTypes = {
    children: PropTypes.node
};

export default function SliderBasic() {
    const [sliderBasicAtom, setSliderBasicAtom] = useRecoilState(sliderBasic);
    const sliderBasicUseSetRecoilState = useSetRecoilState(sliderBasic);

    const [value, setValue] = React.useState<number[]>([sliderBasicAtom.checkBasic]);
    const handleChange = (event: Event, newValue: number | number[]) => {
        setValue(newValue as number[]);
    };
    useEffect(() => {
        const newValue = { checkBasic: value[0] };
        sliderBasicUseSetRecoilState(() => ({ ...sliderBasicAtom, ...newValue }));
    }, [value])

    return (
        <>
            <AirbnbSlider
                components={{ Thumb: AirbnbThumbComponent }}
                marks={marks}
                value={value}
                onChange={handleChange}
                valueLabelDisplay="auto"
                getAriaValueText={valuetext}
                min={1} max={7}
            />
        </>

    );
}