import * as React from 'react';
import Box from '@mui/material/Box';
import Modal from '@mui/material/Modal';
import Button from '@mui/material/Button';
import { useEffect, useState } from 'react';
import ButtonSquareLarge from './ButtonSquareLarge';
import ButtonSquareSmall from './ButtonSquareSmall';

interface Props {
    open: boolean;
    error: string;
}

const PopUpMessage: React.FC<Props> = (props) => {
    const style = {
        position: 'absolute' as 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        width: 400,
        bgcolor: 'background.paper',
        border: '2px solid #000',
        boxShadow: 24,
        pt: 2,
        px: 4,
        pb: 3,
    };

    const [open, setOpen] = useState(false);
    const [error, setError] = useState("test");
    const handleOpen = () => {
        setOpen(true);
    };
    const handleClose = () => {
        setOpen(false);
    };
    useEffect(() => {
        if (props.open == true) {
            setOpen(() => true);
            setError(() => props.error);
        }
    }, [props.open, props.error])
    return (
        <>
            <Modal
                open={open}
                onClose={handleClose}
                aria-labelledby="parent-modal-title"
                aria-describedby="parent-modal-description"
            >
                <Box className="popup-style">
                    {/* <div className="common-flex-row-reverse-wrap-vcenter" style={{ padding: 6 }}>
                        <img src="/asset/img/treatment_icon_x.svg" onClick={handleClose} style={{ cursor: "pointer" }} />
                    </div> */}
                    <div className="media-common-flex-row-wrap-space-between-vcenter">
                        <div className="item-lr">
                            <div className="common-flex-row-wrap-vcenter">
                                <img src="/asset/img/group_register_icon_exclamation.svg" />
                                <span className="font-bold-s18-c000000" style={{ marginLeft: 10 }}>알림</span>
                            </div>
                        </div>
                        <div className="item-lr">
                            <img src="/asset/img/treatment_icon_x.svg" onClick={handleClose} style={{ cursor: "pointer" }} />
                        </div>
                    </div>

                    <div className="container-round-F9F9F9-shadow-padding10" style={{ marginTop: 20 }}>
                        <div className="common-flex-row-wrap-hcenter-vcenter">
                            <span className="font-bold-s16-c000000" style={{ padding: 10 }}>
                                <span className="span-half-2">
                                    {error}
                                </span>
                            </span>
                        </div>
                    </div>

                    <div className="common-flex-row-wrap-hcenter-vcenter" style={{ marginTop: 20 }}>
                        <ButtonSquareSmall background="#573300" onClick={handleClose}>확인</ButtonSquareSmall>
                    </div>

                </Box>
            </Modal>
        </>
    )
}


export default PopUpMessage


