import { useEffect } from "react"
import { useRecoilState, useSetRecoilState } from "recoil"
import { ListViewTitle } from "../../components"
import ButtonSquareSmall from "../../components/ButtonSquareSmall"
import GroupAddTitle from "./components/GroupAddTitle"
import SliderBasic from "../../components/SliderBasic"
import SliderTime from "../../components/SliderTime"
import { GroupAddStep, initGroupAddStepAtom, sliderBasic, sliderTime } from "../../api/atom.groupadd"

interface Step3Props {
    allEdit: boolean;
    linkGroupAddStep: (num: number) => void;
}

const Step3: React.FC<Step3Props> = (props) => {
    const [groupStepAtom, setGroupStepAtom] = useRecoilState<GroupAddStep>(initGroupAddStepAtom);
    const setStepUseSetRecoilState = useSetRecoilState(initGroupAddStepAtom);
    const [sliderBasicAtom, setSliderBasicAtom] = useRecoilState(sliderBasic);
    const [sliderTimeAtom, setSliderTimeAtom] = useRecoilState(sliderTime);

    useEffect(() => {
        const newValue = { therapy_cycle: { count_in_week: sliderBasicAtom.checkBasic, hours_each_therapy: sliderTimeAtom.checkTime } }
        setStepUseSetRecoilState(() => ({ ...groupStepAtom, ...newValue }));
    }, [sliderBasicAtom, sliderTimeAtom])

    return (
        <>
            {/* 치료구분 - title */}
            <div className="common-flex-row-wrap-space-between-vcenter"
                style={{ marginTop: 20 }}>
                <div className="item-lr">
                    <div className="common-flex-row-wrap-vcenter">
                        <span className="font-bold-s21-c573300"><span className="span-half-FFE197">치료주기</span></span>
                    </div>
                </div>
                <div className="item-lr">
                    <div className="common-flex-row-wrap-vcenter">
                        {
                            props.allEdit === true &&
                            <ButtonSquareSmall background="#FF9100">저장</ButtonSquareSmall>
                        }
                        <ButtonSquareSmall background="#FF9100" onClick={() => props.linkGroupAddStep(2)}>이전</ButtonSquareSmall>
                        <ButtonSquareSmall background="#FF9100" onClick={() => props.linkGroupAddStep(4)}>다음</ButtonSquareSmall>
                    </div>
                </div>
            </div>

            <GroupAddTitle background="#00CDB6" image="/asset/img/new_cycle.svg">
                치료주기는 진행횟수와 진행시간을 선택합니다.<br />
                1주 기준으로 진행하고자 하는 횟수와<br />
                1회당 진행하고자하는 시간을 설정합니다.
            </GroupAddTitle>

            <div className="media-group-grey-container" style={{ marginTop: 20 }}>
                <div>
                    <ListViewTitle icon="/asset/img/new_target_02.svg">1주일에 N회</ListViewTitle>
                </div>
                <div className="group-slider-container" style={{ marginTop: 20 }}>
                    <SliderBasic></SliderBasic>
                </div>
            </div>

            <div className="media-group-grey-container" style={{ marginTop: 20 }}>
                <div>
                    <ListViewTitle icon="/asset/img/new_target_02.svg">1주일에 N시간</ListViewTitle>
                </div>
                <div className="group-slider-container" style={{ marginTop: 20 }}>
                    <SliderTime></SliderTime>
                </div>
            </div>
            <div className="media-blank-bottom"></div>
        </>

    )

}
export default Step3


