                {/* <div className="card-content" style={{ marginTop: 5, cursor: 'pointer' }} onClick={onClick}>
                    <img src="/asset/img/non_member_icon_period.svg" style={{ marginLeft: 29 }} />
                    <span className="font-bold-s10-c000000" style={{ marginLeft: 12 }}>2021.09.20 ~ 2021.11.20</span>
                </div>
                <div className="card-content" style={{ cursor: 'pointer' }} onClick={onClick}>
                    <img src="/asset/img/non_member_icon_group.svg" style={{ marginLeft: 26 }} />
                    <span className="font-bold-s10-c000000" style={{ marginLeft: 8 }}>6명 신청중</span>
                    <img src="/asset/img/non_member_icon_smile.svg" style={{ marginLeft: 25 }} />
                    <span className="font-bold-s10-c000000" style={{ marginLeft: 7 }}>홍길동 선생님 외 1명</span>
                </div> */}
