    const [posts, setPosts] = useState<GroupCardsItem[]>([]);
    const [currentPage, setCurrentPage] = useState(1);
    const [postsPerPage] = useState(6);
    const handleChange = (event: ChangeEvent<unknown>, value: number) => {
        setCurrentPage(() => value);
    };
    const indexOfLastPost = currentPage * postsPerPage;
    const indexOfFirstPost = indexOfLastPost - postsPerPage;
    let currentPosts = posts.slice(indexOfFirstPost, indexOfLastPost);
    let totalPage = Math.ceil(posts.length / postsPerPage);


    useEffect(() => {
        Post.getGroupCards('', '언어치료,작업치료', 0, 'id,desc', 10)
            .then((result) => {
                const data: GroupCards = result.data;
                console.log("getPosts", data.content);
                setPosts(() => data.content);
            })
            .catch((err) => {
                console.log("error", err);
            });
    }, [])
    // const [page, setPage] = useState(3);
    // const handleChange = (event: React.ChangeEvent<unknown>, value: number) => {
    //     console.log("value", value);
    //     setPage(value);
