import * as React from "react";
import PropTypes from "prop-types";
import Slider, { SliderThumb } from "@mui/material/Slider";
import { styled } from '@mui/material/styles';
import clsx from "clsx";
import { useEffect } from "react";
import { useRecoilState, useSetRecoilState } from "recoil";
import { sliderAge, sliderYear } from "../api/atom.groupadd";

const AirbnbSlider = styled(Slider)(({ theme }) => ({
    color: "#573300",
    height: 6,
    padding: "13px 0",
    "& .MuiSlider-thumb": {
        height: 30,
        width: 34,
        backgroundColor: "#ECECEC",
        border: "0px solid currentColor",
        "&.first-thumb": {
            border: "0px dashed red"
        },
        "&.second-thumb": {
            border: "0px dashed purple"
        },
        "&:hover": {
            boxShadow: "0 0 0 0px rgba(58, 133, 137, 0.16)"
        },
        "& .airbnb-bar": {
            height: 9,
            width: 1,
            marginLeft: 1,
            marginRight: 1
        },
        "&.first-thumb .airbnb-bar": {
            backgroundColor: "red"
        },
        "&.second-thumb .airbnb-bar": {
            backgroundColor: "currentColor"
        },
        '&:before': {
            display: 'none',
        }
    },
    "& .MuiSlider-track": {
        border: 'none',
        height: 6
    },
    "& .MuiSlider-rail": {
        color: "#ffffff",
        // color: theme.palette.mode === "dark" ? "#ffffff" : "#ffffff",
        // opacity: theme.palette.mode === "dark" ? undefined : 1,
        height: 6
    }
}));
const marks = [
    { value: 0, label: '0', },
    { value: 10, label: '10', },
    { value: 20, label: '20', },
    { value: 30, label: '30', },
    { value: 40, label: '40', },
    { value: 50, label: '50', },
];
function valuetext(value: number) {
    return `${value}세`;
}
interface AirbnbThumbComponentProps extends React.HTMLAttributes<unknown> { }

function AirbnbThumbComponent(props: AirbnbThumbComponentProps) {
    const { children, className, ...other } = props;
    const extraClassName = other["aria-level"] === 0 ? "first-thumb" : "second-thumb";
    return (
        <SliderThumb {...other} className={clsx(className, extraClassName)}>
            {children}
            <span className="rc-slider-handle-l" />
        </SliderThumb>
    );
}

AirbnbThumbComponent.propTypes = {
    children: PropTypes.node
};

export default function SliderMultiAge() {
    const [sliderAgeAtom, setSliderAgeAtom] = useRecoilState(sliderAge);
    const sliderUseSetRecoilState = useSetRecoilState(sliderAge);

    const [value, setValue] = React.useState<number[]>([sliderAgeAtom.min_children_count, sliderAgeAtom.max_children_count]);
    const handleChange = (event: Event, newValue: number | number[]) => {
        setValue(newValue as number[]);
    };
    useEffect(() => {
        //console.log("handleChange", value[0], value[1]);
        const newValue = { min_children_count: value[0], max_children_count: value[1] };
        sliderUseSetRecoilState(() => ({ ...sliderAgeAtom, ...newValue }));
    }, [value])

    return (
        <>
            <AirbnbSlider
                components={{ Thumb: AirbnbThumbComponent }}
                getAriaLabel={(index) =>
                    index === 0 ? "Minimum price" : "Maximum price"
                }
                marks={marks}
                value={value}
                onChange={handleChange}
                valueLabelDisplay="auto"
                getAriaValueText={valuetext}
                min={0} max={50}
            />
        </>

    );
}