import { Checkbox, FormControlLabel, FormGroup, TextField } from "@mui/material";
import { useEffect, useState } from "react";
import { useRecoilState, useSetRecoilState } from "recoil";
import { ListViewTitle, Lists } from "../../components";
import ButtonSquareSmall from "../../components/ButtonSquareSmall";
import GroupAddTitle from "./components/GroupAddTitle";
import SliderMulti from "../../components/SliderMulti";
import { GroupAddStep, initGroupAddStepAtom, initList, ListsArrayProps, ListsQuestionType, sliderYear } from "../../api/atom.groupadd";

interface Step2Props {
    allEdit: boolean;
    linkGroupAddStep: (num: number) => void;
}

const Step2: React.FC<Step2Props> = (props) => {
    const [groupStepAtom, setGroupStepAtom] = useRecoilState<GroupAddStep>(initGroupAddStepAtom);
    const setStepUseSetRecoilState = useSetRecoilState(initGroupAddStepAtom);

    const [sliderYearAtom, setSliderYearAtom] = useRecoilState(sliderYear);
    // Atom저장 참여연령
    useEffect(() => {
        const newAge = { participation_age: sliderYearAtom }
        setStepUseSetRecoilState(() => ({ ...groupStepAtom, ...newAge }));
    }, [sliderYearAtom])

    const [checkBox, setCheckBox] = useState([groupStepAtom.participation_age_check, groupStepAtom.participation_question_check]);
    // Atom저장 연령대무관(1),자유입력(2) 연동규약없음 
    const checkEvent = (itemId: number) => (e: React.ChangeEvent<HTMLInputElement>) => {
        console.log("checkEvent itemId", itemId, e.target.checked);
        if (itemId == 1) {
            const newAgeCheck = { participation_age_check: e.target.checked }
            setStepUseSetRecoilState(() => ({ ...groupStepAtom, ...newAgeCheck }));
        }
        if (itemId == 2) {
            const newQuestionCheck = { participation_question_check: e.target.checked }
            setStepUseSetRecoilState(() => ({ ...groupStepAtom, ...newQuestionCheck }));
        }
    }

    const [lists, setLists] = useState<ListsQuestionType[]>([]);
    // Atom 저장/삭제 참여신청문항 
    const setListsEvent = (list: ListsQuestionType[]) => {
        setLists(() => list);
    }
    // Atom 수정 참여신청문항 
    const setItemUpdate = (itemId: number, value: string) => {
        const data = { id: itemId, question: value }
        const list = lists.map(
            info => info.id === itemId
                ? { ...info, ...data }
                : info
        );
        setLists(() => list);
    }
    useEffect(() => {
        const newQuestions = { participation_questions: { question_responses: lists } }
        setStepUseSetRecoilState(() => ({ ...groupStepAtom, ...newQuestions }));
    }, [lists])

    // SET 참여신청문항 
    useEffect(() => {
        if (groupStepAtom.participation_questions.question_responses.length > 0) {
            setLists(() => groupStepAtom.participation_questions.question_responses);
        }
    }, [groupStepAtom])

    const nextStep = () => {
        props.linkGroupAddStep(3);
    }

    return (
        <>
            {/* 치료구분 - title */}
            <div className="common-flex-row-wrap-space-between-vcenter"
                style={{ marginTop: 20 }}>
                <div className="item-lr">
                    <div className="common-flex-row-wrap-vcenter">
                        <span className="font-bold-s21-c573300"><span className="span-half-FFE197">치료대상</span></span>
                    </div>
                </div>
                <div className="item-lr">
                    <div className="common-flex-row-wrap-vcenter">
                        {
                            props.allEdit === true &&
                            <ButtonSquareSmall background="#FF9100">저장</ButtonSquareSmall>
                        }
                        <ButtonSquareSmall background="#FF9100" onClick={() => props.linkGroupAddStep(1)}>이전</ButtonSquareSmall>
                        <ButtonSquareSmall background="#FF9100" onClick={() => nextStep()}>다음</ButtonSquareSmall>
                    </div>
                </div>
            </div>

            <GroupAddTitle background="#FFBF26" image="/asset/img/new_target_01.svg">
                그룹 구성원의 연령대를 선택합니다.<br />
                0세부터 16세 사이로 선택할 수 있습니다.<br />
                무관한 경우, 체크박스를 선택해주세요.
            </GroupAddTitle>

            <div className="media-group-grey-container" style={{ marginTop: 20 }}>
                <div>
                    <ListViewTitle icon="/asset/img/new_target_02.svg">참여연령</ListViewTitle>
                </div>
                <div className="media-group-slider-container" style={{ marginTop: 20 }}>
                    <SliderMulti></SliderMulti>

                    {/* <TextField id="standard-basic" style={{ width: '100%' }}
                        label={sliderYearAtom.start_age + "세 ~ " + sliderYearAtom.end_age + "세"} variant="standard" aria-readonly /> */}
                    <div style={{ marginTop: 10 }}>
                        {sliderYearAtom.start_age + "세 ~ " + sliderYearAtom.end_age + "세"}
                    </div>
                    <FormGroup style={{ marginTop: 10 }}>
                        <FormControlLabel control={<Checkbox checked={checkBox[0]} onChange={checkEvent(1)} />}
                            label="연령대무관" />
                    </FormGroup>
                </div>
            </div>

            <div className="media-group-grey-container" style={{ marginTop: 20 }}>
                <div style={{ width: "100%" }}>
                    <ListViewTitle icon="/asset/img/new_target_02.svg">참여신청문항</ListViewTitle>
                </div>

                <Lists lists={lists} setListsEvent={setListsEvent} setItemUpdate={setItemUpdate}></Lists>

                <div style={{ width: "100%" }}>
                    <FormGroup style={{ marginTop: 20 }}>
                        <FormControlLabel control={<Checkbox checked={checkBox[1]} onChange={checkEvent(2)} />}
                            label="자유입력 (보호자가 그룹참여 신청 시 아이의 정보를 자유롭게 입력합니다.)" />
                    </FormGroup>
                </div>
            </div>
            <div className="media-blank-bottom"></div>
        </>

    )

}
export default Step2

