import { useEffect, useState } from "react";
import { useRecoilState, useSetRecoilState } from "recoil";
import ButtonSquare from "../../components/ButtonSquare";
import ButtonSquareSmall from "../../components/ButtonSquareSmall";
import GroupAddTitle from "./components/GroupAddTitle";
import { GroupAddStep, initGroupAddStepAtom } from "../../api/atom.groupadd";

interface Step1Props {
    allEdit: boolean;
    linkGroupAddStep: (num: number) => void;
}

const Step1: React.FC<Step1Props> = (props) => {
    // Atom Recoil
    const [groupStepAtom, setGroupStepAtom] = useRecoilState<GroupAddStep>(initGroupAddStepAtom);
    const setStepUseSetRecoilState = useSetRecoilState(initGroupAddStepAtom);

    const [type, setType] = useState([0, 0]);
    // Atom저장 개별치료,그룹치료 
    const onChange = (value: string) => {
        const newType = { therapy_grouping: value };
        setStepUseSetRecoilState(() => ({ ...groupStepAtom, ...newType }));
    }
    // SET 개별치료,그룹치료 
    useEffect(() => {
        console.log("groupStepAtom", groupStepAtom);
        if (groupStepAtom.therapy_grouping == "individual") {
            const newType = [1, 0];
            setType({ ...type, ...newType });
        }
        if (groupStepAtom.therapy_grouping == "group") {
            const newType = [0, 1];
            setType({ ...type, ...newType });
        }
    }, [groupStepAtom])

    return (
        <>

            <div className="common-flex-row-wrap-space-between-vcenter" style={{ marginTop: 20 }}>
                <div className="item-lr">
                    <div className="common-flex-row-wrap-vcenter">
                        <span className="font-bold-s21-c573300"><span className="span-half-FFE197">치료구분</span></span>
                    </div>
                </div>
                <div className="item-lr">
                    <div className="common-flex-row-wrap-vcenter">
                        {
                            props.allEdit === true &&
                            <ButtonSquareSmall background="#FF9100">저장</ButtonSquareSmall>
                        }
                        <ButtonSquareSmall background="#FF9100">초기화</ButtonSquareSmall>
                        <ButtonSquareSmall background="#FF9100" onClick={() => props.linkGroupAddStep(2)}>다음</ButtonSquareSmall>
                    </div>
                </div>
            </div>
            <GroupAddTitle background="#FFEFCB" image="/asset/img/new_classify_03.svg">
                치료구분은 개별치료와 그룹치료로 나누어 집니다.<br />
                개별치료는 아이와 선생님이 1:1로 진행되게 되며,<br />
                그룹치료는 아이와 선생님이 N:N으로 진행하게 됩니다.
            </GroupAddTitle>

            <div className="media-grid--col2__11" style={{ marginTop: 20 }}>
                <div className="box-cgc">
                    <ButtonSquare onClick={() => onChange("individual")} type={type[0]}>
                        <div className="common-flex-column-wrap-hcenter-vcenter">
                            <div className="common-flex-row-wrap-vcenter">
                                <img src="/asset/img/new_classify_icon_01.svg" style={{ marginRight: 10 }} />
                                <span className="font-bold-s15-cffffff">개별치료</span>
                            </div>
                            <span className="font-normal-s15-cffffff">아이와 1:1로 진행할게요!</span>
                        </div>
                    </ButtonSquare>
                </div>
                <div className="box-cgc">
                    <ButtonSquare onClick={() => onChange("group")} type={type[1]}>
                        <div className="common-flex-column-wrap-hcenter-vcenter">
                            <div className="common-flex-row-wrap-vcenter">
                                <img src="/asset/img/new_classify_icon_02.svg" style={{ marginRight: 10 }} />
                                <span className="font-bold-s15-cffffff">그룹치료</span>
                            </div>
                            <span className="font-normal-s15-cffffff">아이와 선생님 다수로 진행할게요!</span>
                        </div>
                    </ButtonSquare>
                </div>
            </div>
            <div className="media-blank-bottom"></div>
        </>

    )

}
export default Step1


