import { LocalizationProvider, DesktopDatePicker } from "@mui/lab";
import AdapterDateFns from "@mui/lab/AdapterDateFns";
import { Box, FormControlLabel, InputBase, Radio, RadioGroup } from "@mui/material"
import { useEffect, useState } from "react";
import ButtonSquareSmall from "../../components/ButtonSquareSmall";
import ButtonSquareLarge from "../../components/ButtonSquareLarge";
import { useForm } from 'react-hook-form';
import { signUpValidation } from "../../api/yup";
import { yupResolver } from '@hookform/resolvers/yup';
import FormErrorMessage from "../../components/FormErrorMessage";
import { useRecoilState, useSetRecoilState } from "recoil";
import { AccountJoin, AccountJoinAtom } from "../../api/atom.sign";
import moment from "moment";
import DragDropInput, { IFileTypes } from "./DragDropInput";

interface SignUpFormProps {
    clickEvnet: (num: number) => void;
}

type SignUpInputType = {
    email: string;
    name: string;
    password: string;
    password2: string;
    gender: string;
    birth: string;
    phone: string;
};
const SignUpForm: React.FC<SignUpFormProps> = (props) => {
    const [signUpAtom, setSignUpAtom] = useRecoilState<AccountJoin>(AccountJoinAtom);
    const setSignUpUseSetRecoilState = useSetRecoilState(AccountJoinAtom);
    useEffect(() => {
        console.log(signUpAtom);
    }, [signUpAtom])

    const initInputItem = {
        email: '',
        name: '',
        gender: '',
        birth: '',
        phone: '',
        address: {
            street: "",
            detail: ""
        }
    }
    const [inputs, setInputs] = useState(initInputItem);
    const onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const { name, value } = e.target;
        setInputs({ ...inputs, [name]: value });
        if (name == "password") {
            const password = { password: { authenticated_string: value } };
            setSignUpUseSetRecoilState(() => ({ ...signUpAtom, ...password }));
        }
    }
    //--- file

    // const [file, setFile] = useState<File[]>(File[]);
    //--- file
    //--- birth
    const [birth, setBirth] = useState<Date | null>(null);
    const [tmpBirth, setTmpBirth] = useState("");
    const handleBirthDateChange = (date: Date | null) => {

        if (date != null) {
            console.log("handleBirthDateChange >>>> date", date);
            setBirth(() => date);
            console.log("1");
            setTmpBirth(() => moment(date).format('YYYY-MM-DD'));
            console.log("2", moment(date).format('YYYY-MM-DD'));
        }

    };
    useEffect(() => {
        console.log("3");
        setInputs({ ...inputs, "birth": tmpBirth });
    }, [tmpBirth])
    //--- birth
    //--- gender
    const [gender, setGender] = useState('female');
    const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        setGender(() => event.target.value);
    };
    useEffect(() => {
        setInputs({ ...inputs, "gender": gender });
    }, [gender])
    //--- gender
    const { handleSubmit, formState: { errors }, register } = useForm<SignUpInputType>({
        resolver: yupResolver(signUpValidation),
    });
    const [errorMsg, setErrorMsg] = useState("");
    const onSubmit = handleSubmit((data: SignUpInputType) => {
        //console.log(data);
        //console.log("inputs", inputs);
        if (tmpBirth == "") {
            setErrorMsg("생일을 입력해주세요.");
        } else {
            setErrorMsg("");
            const memberInfo = {
                member_info: inputs
            }
            setSignUpUseSetRecoilState(() => ({ ...signUpAtom, ...memberInfo }));
            props.clickEvnet(4);
        }
    });
    // useEffect(() => {
    //     console.log("4", inputs);
    // }, [inputs])

    return (
        <>
            <form onSubmit={onSubmit}>
                <div className="sign-line"></div>
                <div className="common-flex-row-wrap-vcenter" style={{ marginLeft: 20, marginRight: 20 }}>
                    <div style={{ width: 100 }}><span className="font-bold-s12-c000000">이메일*</span></div>
                    <div style={{ width: 280 }}>
                        <InputBase sx={{ ml: 1, flex: 1, width: '100%' }}
                            placeholder="admin@lumanlanb.com"
                            {...register('email')}
                            onChange={onChange} />
                        {errors.email && (
                            <FormErrorMessage errorMessage={errors.email.message!} />
                        )}
                    </div>
                </div>

                <div className="sign-line"></div>
                <div className="common-flex-row-wrap-vcenter" style={{ marginLeft: 20, marginRight: 20 }}>
                    <div style={{ width: 100 }}><span className="font-bold-s12-c000000">비밀번호*</span></div>
                    <div style={{ width: 260 }}>
                        <InputBase sx={{ ml: 1, flex: 1, width: '100%' }}
                            type="password" placeholder="**********"
                            {...register('password')}
                            onChange={onChange} />
                        {errors.password && (
                            <FormErrorMessage errorMessage={errors.password.message!} />
                        )} </div>
                </div>

                <div className="sign-line"></div>
                <div className="common-flex-row-wrap-vcenter" style={{ marginLeft: 20, marginRight: 20 }}>
                    <div style={{ width: 100 }}><span className="font-bold-s12-c000000">비밀번호 확인*</span></div>
                    <div style={{ width: 260 }}>
                        <InputBase sx={{ ml: 1, flex: 1, width: '100%' }} type="password" placeholder="**********"
                            {...register('password2')}
                            onChange={onChange} />
                        {errors.password2 && (
                            <FormErrorMessage errorMessage={errors.password2.message!} />
                        )}
                    </div>
                </div>

                <div className="sign-line"></div>
                <div className="common-flex-row-wrap-vcenter" style={{ marginLeft: 20, marginRight: 20 }}>
                    <div style={{ width: 100 }}><span className="font-bold-s12-c000000">이름*</span></div>
                    <div style={{ width: 260 }}>
                        <InputBase sx={{ ml: 1, flex: 1, width: '100%' }} placeholder="홍길동"
                            {...register('name')}
                            onChange={onChange} />
                        {errors.name && (
                            <FormErrorMessage errorMessage={errors.name.message!} />
                        )}
                    </div>
                </div>


                <div className="sign-line"></div>
                <div className="common-flex-row-wrap-vcenter" style={{ marginLeft: 20, marginRight: 20 }}>
                    <div style={{ width: 100 }}><span className="font-bold-s12-c000000">성별*</span></div>
                    <div style={{ width: 260 }}>

                        <RadioGroup
                            row
                            name="controlled-radio-buttons-group"
                            value={gender}
                            onChange={handleChange}
                        >
                            <FormControlLabel {...register('gender')} value="female" control={<Radio size="small" />} label="여성" />
                            <FormControlLabel {...register('gender')} value="male" control={<Radio size="small" />} label="남성" />
                        </RadioGroup>
                    </div>
                </div>

                <div className="sign-line"></div>
                <div className="common-flex-row-wrap-vcenter" style={{ marginLeft: 20, marginRight: 20 }}>
                    <div style={{ width: 100 }}><span className="font-bold-s12-c000000">생년월일*</span></div>

                    <LocalizationProvider dateAdapter={AdapterDateFns}>
                        <DesktopDatePicker
                            label=""
                            value={birth}
                            inputFormat={"yyyy-MM-dd"}
                            mask={"____-__-__"}
                            {...register('birth')}
                            onChange={(date) => handleBirthDateChange(date)}
                            renderInput={({ inputRef, inputProps, InputProps }) => (
                                <Box sx={{ display: 'flex', alignItems: 'center', flexGrow: 1 }}>
                                    <input ref={inputRef} {...inputProps} style={{ border: "none", flexGrow: 1, height: 30 }} />
                                    {InputProps?.endAdornment}
                                </Box>
                            )}
                        />
                    </LocalizationProvider>
                    {errorMsg === "" ? <></> : <FormErrorMessage errorMessage={errorMsg} />}
                </div>

                <div className="sign-line"></div>
                <div className="common-flex-row-wrap-vcenter" style={{ marginLeft: 20, marginRight: 16 }}>
                    <div style={{ width: 100 }}><span className="font-bold-s12-c000000">연락처*</span></div>
                    <InputBase sx={{ ml: 1, flex: 1 }}
                        placeholder="010-0000-0000"
                        {...register('phone')}
                        onChange={onChange} />
                    {errors.phone && (
                        <FormErrorMessage errorMessage={errors.phone.message!} />
                    )}
                    <ButtonSquareSmall>본인인증</ButtonSquareSmall>
                </div>


                <div className="sign-line"></div>
                <div className="signup-upload" style={{ marginTop: 10, marginLeft: 20, marginRight: 20, alignItems: "center" }}>
                    {/* <img src="/asset/img/myinfo_teacher_Icon_metro-attachment.svg" style={{ marginRight: 10 }} />
                    자격증 첨부, 기관회원의 경우 사업자등록증을 첨부해주세요. */}
                    <DragDropInput ></DragDropInput>
                </div>


                <div style={{ paddingLeft: 20, paddingRight: 20 }}>
                    <ButtonSquareLarge onClick={() => onSubmit()}>다음</ButtonSquareLarge>
                </div>
                <div style={{ paddingLeft: 20, paddingRight: 20 }}>
                    <ButtonSquareLarge onClick={() => props.clickEvnet(2)}>이전</ButtonSquareLarge>
                </div>

            </form>
        </>
    )
}
export default SignUpForm


