import { InputAdornment, TextField } from "@mui/material"
import { useState, useEffect, useRef } from "react"
import { useRecoilState, useSetRecoilState } from "recoil"
import { ListViewTitle, Lists } from "../../components"
import ButtonSquareSmall from "../../components/ButtonSquareSmall"
import GroupAddTitle from "./components/GroupAddTitle"
import ListsDetail from "../../components/ListsDetail"
import { GroupAddStep, initGroupAddStepAtom, ListsDetailServerType, ListsDetailType } from "../../api/atom.groupadd"


interface Step7Props {
    allEdit: boolean;
    linkGroupAddStep: (num: number) => void;
}

const Step7: React.FC<Step7Props> = (props) => {
    const [groupStepAtom, setGroupStepAtom] = useRecoilState<GroupAddStep>(initGroupAddStepAtom);
    const setStepUseSetRecoilState = useSetRecoilState(initGroupAddStepAtom);

    const [inputText, setInputText] = useState(groupStepAtom.therapy_objective_response.group_objective);
    const onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        setInputText(() => e.target.value);
    }
    const [lists, setLists] = useState<ListsDetailType[]>([]);
    // Atom 저장/삭제 참여신청문항 
    const setListsEvent = (list: ListsDetailType[]) => {
        setLists(() => list);
    }
    // Atom 수정 참여신청문항 
    const setItemUpdate = (itemId: number, value: string) => {
        const data = { id: itemId, detail: value }
        const list = lists.map(
            info => info.id === itemId
                ? { ...info, ...data }
                : info
        );
        setLists(() => list);
    }
    useEffect(() => {
        const newValue = { therapy_objective_response: { group_objective: inputText, detail_objective_responses: lists } }
        setStepUseSetRecoilState(() => ({ ...groupStepAtom, ...newValue }));
    }, [lists])

    useEffect(() => {
        const newValue = { therapy_objective_response: { group_objective: inputText, detail_objective_responses: lists } }
        setStepUseSetRecoilState(() => ({ ...groupStepAtom, ...newValue }));
    }, [inputText])
    const nextId = useRef(0);
    useEffect(() => {
        console.log("length" + groupStepAtom.participation_questions.question_responses.length);
        if (groupStepAtom.therapy_objective_response.detail_objective_responses.length > 0) {
            const tmp = groupStepAtom.therapy_objective_response.detail_objective_responses;
            let tmpArray: ListsDetailType[] = [];
            tmp.map((item: ListsDetailServerType, index: number) => {
                const id = nextId.current += 1;
                const data = {
                    id: nextId.current,
                    detail: item.detail,
                };
                tmpArray.push(data);
            });
            setLists(() => tmpArray);

        }
        return () => {
            console.log('컴포넌트가 화면에서 사라짐');
        };

    }, [])

    return (
        <>
            {/* 치료구분 - title */}
            <div className="common-flex-row-wrap-space-between-vcenter"
                style={{ marginTop: 20 }}>
                <div className="item-lr">
                    <div className="common-flex-row-wrap-vcenter">
                        <span className="font-bold-s21-c573300"><span className="span-half-FFE197">치료계획</span></span>
                    </div>
                </div>
                <div className="item-lr">
                    <div className="common-flex-row-wrap-vcenter">
                        {
                            props.allEdit === true &&
                            <ButtonSquareSmall background="#FF9100">저장</ButtonSquareSmall>
                        }
                        <ButtonSquareSmall background="#FF9100" onClick={() => props.linkGroupAddStep(6)}>이전</ButtonSquareSmall>
                        <ButtonSquareSmall background="#FF9100" onClick={() => props.linkGroupAddStep(8)}>다음</ButtonSquareSmall>
                    </div>
                </div>
            </div>

            <GroupAddTitle background="#C9E84E" image="/asset/img/new_plan.svg">
                그룹 목표와 상세 목표를 기재합니다.<br />
                입력한 내용은 그룹 상세정보에서 확인할 수 있으며,<br />
                상세 목표는 다수로 입력할 수 있습니다.
            </GroupAddTitle>


            <div className="media-group-grey-container" style={{ marginTop: 20 }}>
                <div>
                    <ListViewTitle icon="/asset/img/new_target_02.svg">그룹목표</ListViewTitle>
                </div>
                <div>

                    <TextField id="standard-basic"
                        style={{ width: '100%', marginTop: 20 }}
                        placeholder="대화능력기르기"
                        variant="standard"
                        value={inputText}
                        onChange={onChange}
                        InputProps={{
                            startAdornment: (
                                <InputAdornment position="start">
                                    <img src="/asset/img/treatment_icon_pencil.svg" />
                                </InputAdornment>
                            )
                        }} />
                </div>
            </div>

            <div className="media-group-grey-container" style={{ marginTop: 20 }}>
                <div style={{ width: "100%" }}>
                    <ListViewTitle icon="/asset/img/new_target_02.svg">상세목표</ListViewTitle>
                </div>
                <ListsDetail lists={lists} setListsEvent={setListsEvent} setItemUpdate={setItemUpdate}></ListsDetail>

            </div>
            <div className="media-blank-bottom"></div>
        </>

    )

}
export default Step7


