import { FormControl, TextField, InputAdornment } from "@mui/material"
import { useState } from "react";
import ButtonSquareLarge from "../../components/ButtonSquareLarge";
import NativeSelect from '@mui/material/NativeSelect';
import { makeStyles } from "@mui/styles";
import { GroupJoinParentsArray, initGroupJoinParentsItem } from "../../api/atom.groupjoin";
import { groupjoinParentsValidation } from "../../api/yup";
import { yupResolver } from '@hookform/resolvers/yup';
import { useForm } from 'react-hook-form';
import FormErrorMessage from '../../components/FormErrorMessage';

interface localProps {

}

const GroupJoinParents: React.FC<localProps> = (props) => {

    const useStylesNativeSelect = makeStyles(theme => ({
        formControl: {
            margin: 0,
            minWidth: 130,
            Height: 20
        }
    }));
    const classes = useStylesNativeSelect();
    //--- Radio Button
    const [inputSelect, setInputSelect] = useState("김가가3 어린이");
    const onChangeInputSelect = (e: string) => {
        console.log("inputSelect", e);
        setInputSelect(() => e);
    }
    //--- inputs
    const [inputs, setInputs] = useState(initGroupJoinParentsItem);
    const onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const { name, value } = e.target;
        setInputs({ ...inputs, [name]: value });
    }
    const { handleSubmit, formState: { errors }, register } = useForm<GroupJoinParentsArray>({
        resolver: yupResolver(groupjoinParentsValidation),
    });
    const onSubmit = handleSubmit((data: GroupJoinParentsArray) => {
        console.log("GroupJoinParentsArray", data);
    });

    return (
        <>
            <form onSubmit={onSubmit}>
                <div className="group-info-container-sky" style={{ padding: 20, marginTop: 20, marginBottom: 20 }}>
                    <div className="common-flex-row-wrap-space-between-vcenter">
                        <div className="common-row-wrap-vstretch">
                            <div className="group-select-box">
                                <FormControl className={classes.formControl}>
                                    <NativeSelect
                                        value={inputSelect}
                                        onChange={event => onChangeInputSelect(event.target.value)}
                                        inputProps={{
                                            name: 'age',
                                            id: 'uncontrolled-native',
                                        }}
                                        disableUnderline
                                    >
                                        <option value={"김가가1 어린이"}>김가가1 어린이</option>
                                        <option value={"김가가2 어린이"}>김가가2 어린이</option>
                                        <option value={"김가가3 어린이"}>김가가3 어린이</option>
                                    </NativeSelect>
                                </FormControl>
                            </div>
                        </div>
                        <div>

                            <span className="font-normal-s15-c573300">2000-01-14 / 여아</span>
                            <img src="/asset/img/group_register_icon_girl.svg" style={{ marginLeft: 10 }} />
                        </div>
                    </div>

                    <div className="media-container-round-white-padding40" style={{ marginTop: 20 }}>
                        <div>
                            <span className="font-bold-s18-c573300">
                                Q. 아이가 이름을 들으면 반응하나요?</span>
                            <TextField id="standard-basic"
                                style={{ width: '100%' }}
                                placeholder="예) 아이가 이름을 들으면 하던 행동을 멈추고 돌아봅니다."
                                {...register('question1')}
                                value={inputs.question1 || ""}
                                onChange={onChange}
                                variant="standard" />
                            {errors.question1 && (
                                <FormErrorMessage errorMessage={errors.question1.message!} />
                            )}
                            <br /><br />
                            <span className="font-bold-s18-c573300">
                                Q. 아이가 가갸거겨를 발음할 수 있나요?</span>
                            <TextField id="standard-basic"
                                style={{ width: '100%' }}
                                placeholder="예) 아이가 이름을 들으면 하던 행동을 멈추고 돌아봅니다."
                                {...register('question2')}
                                value={inputs.question2 || ""}
                                onChange={onChange}
                                variant="standard" />
                            {errors.question2 && (
                                <FormErrorMessage errorMessage={errors.question2.message!} />
                            )}
                        </div>
                        <div style={{ marginTop: 30 }}>

                            <TextField
                                fullWidth
                                id="outlined-multiline-static"
                                label=""
                                multiline
                                rows={10}
                                {...register('memo')}
                                value={inputs.memo || ""}
                                onChange={onChange}
                                defaultValue="이전 수업보다 수월하게 따라하는 것 같습니다."
                                InputProps={{
                                    style: {
                                        paddingLeft: 30,
                                        backgroundColor: '#f9f9f9',
                                    },
                                    startAdornment: (
                                        <InputAdornment position="start" style={{ position: "absolute", top: 27, left: 10 }}>
                                            <img src="/asset/img/treatment_icon_pencil.svg" />
                                        </InputAdornment>
                                    )
                                }}
                            />
                            {errors.memo && (
                                <FormErrorMessage errorMessage={errors.memo.message!} />
                            )}
                        </div>
                    </div>


                    <div style={{ marginTop: 20 }}>
                        <ButtonSquareLarge onClick={() => onSubmit()} background="#00CDB6">등록</ButtonSquareLarge>
                    </div>
                </div>
            </form>


        </>

    )

}
export default GroupJoinParents


