
import { atom } from 'recoil';
// 로그인
export interface AccountLogin {
    email: string;
    password: string;
  }
export const initLogin = {
    email: '',
    password: '',
}
export interface ReturnLogin {
	accessToken: string;
    accessTokenExpiredAt: string;
	refreshToken: string;
	refreshTokenExpiredAt: string;
}

// 회원가입 
export interface AccountJoin {
	therapist: boolean;
    individual: boolean;
	password: { 
		authenticated_string: string 
	};
	member_info: {
		name: string;
		email: string;
		gender: string;
		birth: string;
		phone: string;
		address: {
			street: string;
			detail: string;
		}
	}
}
export const initAccountJoin = {
	therapist: false,
    individual: false,
	password: {
		authenticated_string: ""
	},
	member_info: {
		name: "",
		email: "",
		gender: "",
		birth: "",
		phone: "",
		address: {
			street: "",
			detail: ""
		}
	}
}
export const AccountJoinAtom = atom<AccountJoin>({
    key: 'AccountJoinAtom', 
    default: initAccountJoin
});