import { ChipLarge, Footer, Header } from "../components"
import 'moment/locale/ko'; // KTS
import { useState } from 'react';
import ParentsInfo from "./MyInfo/ParentsInfo";
import ChildInfo from "./MyInfo/ChildInfo";
import OrderInfo from "./MyInfo/OrderInfo";
import Like from "./MyInfo/Like";
import GroupInfo from "./MyInfo/GroupInfo";
import Join from "./MyInfo/Join";

interface Props {

}

const MyInfo: React.FC<Props> = (props) => {

    const [menu, setMenu] = useState(1);
    const [menuArray, setMenuArray] = useState([0, 1, 0, 0, 0, 0, 0, 0]);

    const changeMenu = (num: number) => {
        setMenu(() => num);
        console.log("menu", menu);
        const newArr = [...menuArray];
        menuArray.map((item: number, index: number) => {
            if (num == index) {
                newArr[index] = 1;
            } else {
                newArr[index] = 0;
            }
            setMenuArray(() => newArr);
        }
        );
    }
    const clickMenu = (num: number) => {
        changeMenu(num);
    };

    let component = <ParentsInfo></ParentsInfo>;
    switch (menu) {
        case 1: component = (<ParentsInfo></ParentsInfo>); break;
        case 2: component = (<ChildInfo></ChildInfo>); break;
        case 3: component = (<OrderInfo></OrderInfo>); break;
        case 4: component = (<Like></Like>); break;
        case 5: component = (<GroupInfo></GroupInfo>); break;
        case 6: component = (<Join></Join>); break;
        default: component = (<ParentsInfo></ParentsInfo>); break;
    }

    return (
        <div className="common-bg-myinfo">
            <Header
                className="group-add-header"
                goodroad="/asset/img/subpage_top_tabbar_logo.svg"
                person="/asset/img/new_classify_02.svg"
                bell="/asset/img/bell.svg"
                kakao="/asset/img/subpage_top_tabbar_icon_message.svg"
                color="font-bold-s12-cffffff"
            />
            <div className="media-wrapper">
                <div className="common-wrapper__container"></div>
                <div className="common-wrapper__container">
                    {/* ######################### Content Area Begin ######################### */}
                    <div style={{ padding: 10 }}>
                        <div className="common-flex-row-wrap-vcenter" style={{ marginTop: 10 }}>
                            <ChipLarge onClick={() => clickMenu(1)} type={menuArray[1]}>보호자 정보</ChipLarge>
                            <ChipLarge onClick={() => clickMenu(2)} type={menuArray[2]}>어린이 정보</ChipLarge>
                            <ChipLarge onClick={() => clickMenu(3)} type={menuArray[3]}>맞춤정보</ChipLarge>
                            <ChipLarge onClick={() => clickMenu(4)} type={menuArray[4]}>좋아요</ChipLarge>
                            <ChipLarge onClick={() => clickMenu(5)} type={menuArray[5]}>그룹정보</ChipLarge>
                            <ChipLarge onClick={() => clickMenu(6)} type={menuArray[6]}>참여신청</ChipLarge>
                        </div>
                        {component}
                        <div className="media-blank-bottom"></div>
                    </div>

                    {/* ######################### Content Area End   ######################### */}
                </div>
                <div className="common-wrapper__container"></div>
                <Footer></Footer>
            </div>
        </div>
    )

}

export default MyInfo

