import * as React from 'react';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import IconButton from '@mui/material/IconButton';
import AddBoxIcon from '@mui/icons-material/AddBox';
import IndeterminateCheckBoxIcon from '@mui/icons-material/IndeterminateCheckBox';
import { InputAdornment, InputBase } from '@mui/material';
import { useState, useRef, useEffect } from 'react';
import TextField from '@mui/material/TextField';
import { ListsArrayProps, ListsDetailType } from '../api/atom.groupadd';

interface ListsProps {
    setItemUpdate: (itemId: number, value: string) => void;
    lists: ListsDetailType[];
    setListsEvent: (list: ListsDetailType[]) => void;
}

const ListsDetail: React.FC<ListsProps> = (props) => {
    const [errorMessage, setErrorMessage] = React.useState("");
    const nextId = useRef(0);
    const [inputText, setInputText] = useState("");

    const onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        setInputText(e.target.value);
        setErrorMessage("");
    }
    const clickAddEvent = () => {
        if (inputText == "") {
            setErrorMessage(
                "문항을 입력해주세요!"
            );
        } else {
            const id = nextId.current += 1;
            const list = {
                id: nextId.current,
                detail: inputText,
            };
            props.setListsEvent(props.lists.concat(list));
        }

    };
    const clickDeleteEvent = (itemId: number) => {
        props.setListsEvent(props.lists.filter(list => list.id !== itemId));
    };
    const onItemChange = (itemId: number) => (e: React.ChangeEvent<HTMLInputElement>) => {
        console.log(itemId, e.target.value);
        props.setItemUpdate(itemId, e.target.value);
    }
    useEffect(() => {
        console.log("props.lists", props.lists);
    }, [props.lists])

    return (
        <>
            <div style={{ paddingTop: 10, paddingRight: 10, paddingBottom: 10 }}>
                <TextField
                    error={inputText.length == 0}
                    name="text"
                    size="small"
                    sx={{ ml: 1, flex: 1, width: '100%' }}
                    id="standard-error"
                    label="입력하기"
                    variant="standard"
                    onChange={onChange}
                    value={inputText}
                    helperText={errorMessage}
                    InputProps={{
                        disableUnderline: true,
                        startAdornment: (
                            <InputAdornment position="start">
                                <img src="/asset/img/treatment_icon_pencil.svg" />
                            </InputAdornment>
                        ),
                        endAdornment: (
                            < InputAdornment position="end" >
                                <IconButton aria-label="toggle password visibility">
                                    <AddBoxIcon onMouseDown={() => clickAddEvent()} />
                                </IconButton>
                            </InputAdornment>
                        )
                    }}
                />
            </div>
            <List sx={{ width: '100%', bgcolor: 'transparent' }} disablePadding>
                {props.lists.map((item, index) => (
                    <ListItem
                        disableGutters={true}
                        key={item.id}
                        secondaryAction={
                            <IconButton>
                                <IndeterminateCheckBoxIcon onClick={() => clickDeleteEvent(item.id)} />
                            </IconButton>
                        }
                        divider={false}
                    >
                        {/* <ListItemText primary={item.detail} /> */}
                        <TextField id="standard-basic"
                            sx={{ ml: 1, flex: 1, width: '100%' }}
                            value={item.detail}
                            onChange={onItemChange(item.id)}
                            variant="standard" />
                    </ListItem>
                ))}
            </List>
        </>
    )
}
export default ListsDetail