import { TextField, InputAdornment } from "@mui/material"
import { useState } from "react";
import ButtonSquareLarge from "../../components/ButtonSquareLarge"
import FormErrorMessage from '../../components/FormErrorMessage';
import { GroupInviteArray, initInvitehreapistItem } from "../../api/atom.groupjoin";
import { yupResolver } from '@hookform/resolvers/yup';
import { useForm } from 'react-hook-form';
import { groupInviteValidation } from "../../api/yup";

interface localProps {

}

const GroupInvite: React.FC<localProps> = (props) => {
    //--- inputs
    const [inputs, setInputs] = useState(initInvitehreapistItem);
    const onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const { name, value } = e.target;
        setInputs({ ...inputs, [name]: value });
    }
    const { handleSubmit, formState: { errors }, register } = useForm<GroupInviteArray>({
        resolver: yupResolver(groupInviteValidation),
    });
    const onSubmit = handleSubmit((data: GroupInviteArray) => {
        console.log("GroupJoinParentsArray", data);
    });

    return (
        <>
            <form onSubmit={onSubmit}>
                <div className="group-info-container-sky" style={{ padding: 20, marginTop: 20, marginBottom: 20 }}>
                    <div className="media-container-round-white-padding40" style={{ marginTop: 20 }}>
                        <div className="common-flex-row-wrap-vcenter" style={{ marginTop: 20, marginBottom: 20 }}>
                            <div className="common-flex-column-wrap">
                                <span className="font-bold-s21-c573300">
                                    거절사유*
                                </span>
                            </div>
                        </div>

                        <div style={{ marginTop: 30 }}>

                            <TextField
                                fullWidth
                                id="outlined-multiline-static"
                                label=""
                                multiline
                                {...register('memo')}
                                value={inputs.memo || ""}
                                onChange={onChange}
                                rows={10}
                                defaultValue="아이의 참여가 힘들다고 판단된 경우, 상세사유를 기재해주세요."
                                InputProps={{
                                    style: {
                                        paddingLeft: 30,
                                        backgroundColor: '#f9f9f9',
                                    },
                                    startAdornment: (
                                        <InputAdornment position="start" style={{ position: "absolute", top: 27, left: 10 }}>
                                            <img src="/asset/img/treatment_icon_pencil.svg" />
                                        </InputAdornment>
                                    )
                                }}
                            />
                            {errors.memo && (
                                <FormErrorMessage errorMessage={errors.memo.message!} />
                            )}
                        </div>
                    </div>

                    <div className="media-grid--col2__11" style={{ marginTop: 20 }}>
                        <div className="box-cgc">
                            <ButtonSquareLarge background="#00CDB6">초대거절</ButtonSquareLarge>
                        </div>
                        <div className="box-cgc">
                            <ButtonSquareLarge background="#00CDB6">초대승인</ButtonSquareLarge>
                        </div>
                    </div>
                </div>
            </form>
        </>

    )

}
export default GroupInvite


