import * as React from 'react';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import IconButton from '@mui/material/IconButton';
import AddBoxIcon from '@mui/icons-material/AddBox';
import IndeterminateCheckBoxIcon from '@mui/icons-material/IndeterminateCheckBox';
import { Checkbox, InputAdornment, InputBase } from '@mui/material';
import { useState, useRef, useEffect } from 'react';
import TextField from '@mui/material/TextField';
import TimeInput from 'react-input-time';
import { initScheduleLists, MemoLists, ScheduleLists } from '../../../api/atom.myinfo';
import moment from "moment";

interface Props {
    lists: MemoLists[];
    changeLists: (data: MemoLists[]) => void;
}

const MemoListEditor: React.FC<Props> = (props) => {
    moment.locale('ko');
    const [lists, setLists] = useState<MemoLists[]>(props.lists);
    const [errorMessage, setErrorMessage] = React.useState("");
    const nextId = useRef(lists.length);


    // 추가 

    const [inputText, setInputText] = useState("");

    const onChangeText = (e: React.ChangeEvent<HTMLInputElement>) => {
        setInputText(e.target.value);
        setErrorMessage("");
    }
    const clickAddEvent = () => {
        if (inputText == "") {
            setErrorMessage("내용을 입력해주세요!");
        } else {
            const id = nextId.current += 1;
            const list = {
                id: nextId.current,
                check: false,
                text: inputText,
            };
            setLists(() => lists.concat(list));
        }
    };
    // 수정 
    const textEvent = (itemId: number) => (e: React.ChangeEvent<HTMLInputElement>) => {
        console.log("textEvent itemId", itemId, e.target.value);
        const data = {
            id: itemId,
            text: e.target.value,
        };
        setLists(() =>
            lists.map(
                info => info.id === itemId
                    ? { ...info, ...data }
                    : info
            ));
    };
    const checkEvent = (itemId: number) => (e: React.ChangeEvent<HTMLInputElement>) => {
        console.log("checkEvent itemId", itemId, e.target.checked);
        const data = {
            id: itemId,
            check: e.target.checked,
        };
        setLists(() =>
            lists.map(
                info => info.id === itemId
                    ? { ...info, ...data }
                    : info
            ));
    }


    // 삭제 
    const clickDeleteEvent = (itemId: number) => {
        console.info('You clicked clickDeleteEvent.');
        setLists(lists.filter(list => list.id !== itemId));
    };

    useEffect(() => {
        props.changeLists(lists);
    }, [lists]);

    return (
        <>

            <TextField
                error={inputText.length == 0}
                name="text"
                size="small"
                sx={{ ml: 1, flex: 1 }}
                id="outlined-error"
                label=""
                variant="standard"
                onChange={onChangeText}
                value={inputText}
                helperText={errorMessage}
                inputProps={{ style: { fontSize: 13 } }} // font size of input text
                InputLabelProps={{ style: { fontSize: 13 } }} // font size of input label
                InputProps={{
                    disableUnderline: true,
                    startAdornment: (
                        <InputAdornment position="start">
                            <img src="/asset/img/treatment_icon_pencil.svg" />
                        </InputAdornment>
                    ),
                    endAdornment: (
                        < InputAdornment position="end" >
                            <IconButton aria-label="toggle password visibility">
                                <AddBoxIcon onMouseDown={() => clickAddEvent()} />
                            </IconButton>
                        </InputAdornment>
                    )
                }}
            />

            < List sx={{ width: '100%', bgcolor: 'transparent' }} disablePadding >
                {
                    lists.map((item, index) => (
                        <ListItem
                            disableGutters={true}
                            key={item.id}
                            secondaryAction={
                                <IconButton>
                                    <IndeterminateCheckBoxIcon onMouseDown={() => clickDeleteEvent(item.id)} />
                                </IconButton>
                            }
                            divider={true}
                            style={{ padding: 0, margin: 0 }}
                        >
                            <Checkbox size="small"
                                checked={item.check}
                                onChange={checkEvent(item.id)}
                                style={{ padding: 0, margin: 0 }} />

                            <TextField
                                error={item.text.length == 0}
                                name="text"
                                size="small"
                                sx={{ ml: 1, flex: 1 }}
                                id="outlined-error"
                                label=""
                                variant="standard"
                                onChange={textEvent(item.id)}
                                value={item.text}
                                helperText={errorMessage}
                                inputProps={{ style: { fontSize: 13 } }} // font size of input text
                                InputLabelProps={{ style: { fontSize: 13 } }} // font size of input label
                                InputProps={{
                                    disableUnderline: true,
                                    startAdornment: (
                                        <InputAdornment position="start">
                                            <img src="/asset/img/treatment_icon_pencil.svg" />
                                        </InputAdornment>
                                    )
                                }}
                            />
                        </ListItem>
                    ))
                }
            </List >
        </>
    )
}
export default MemoListEditor