import { Box, FormControlLabel, InputAdornment, InputBase, Radio, RadioGroup, TextField } from '@mui/material';
import * as React from 'react';
import { ChildInfoListArray } from '../../../api/atom.myinfo';
import { childInfoValidation } from '../../../api/yup';
import ButtonSquareLineB from '../../../components/ButtonSquareLineB';
import { yupResolver } from '@hookform/resolvers/yup';
import { useForm } from 'react-hook-form';
import { useEffect, useState } from 'react';
import { LocalizationProvider, DesktopDatePicker } from '@mui/lab';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import moment from "moment";
import FormErrorMessage from '../../../components/FormErrorMessage';

interface Props {
    clickAddEvent: () => void;
    item: ChildInfoListArray;
    clickDeleteEvent: (itemId: number) => void;
    clickUpdateEvent: (itemId: number, data: ChildInfoListArray) => void;
}

const ChildInfoLists: React.FC<Props> = (props) => {

    const [edit, setEdit] = useState(false);
    const onEdit = () => {
        setEdit(() => !edit);
    }
    const initInputItem = {
        id: props.item.id,
        name: props.item.name,
        gender: props.item.gender,
        birth: props.item.birth,
        memo: props.item.memo,
    }
    const [inputs, setInputs] = useState(initInputItem);
    const onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const { name, value } = e.target;
        setInputs({ ...inputs, [name]: value });
    }
    const { handleSubmit, formState: { errors }, register } = useForm<ChildInfoListArray>({
        resolver: yupResolver(childInfoValidation),
    });
    const [errorMsg, setErrorMsg] = useState("");
    const onSubmit = handleSubmit((data: ChildInfoListArray) => {
        console.log("ChildInfoListArray", data);
        if (tmpBirth == "") {
            setErrorMsg("생일을 입력해주세요.");
        } else {
            setErrorMsg("");
            props.clickUpdateEvent(props.item.id, inputs);
        }
    });

    //--- birth
    const [birth, setBirth] = useState<Date | null>(null);
    const [tmpBirth, setTmpBirth] = useState("");
    const handleBirthDateChange = (date: Date | null) => {
        console.log("handleBirthDateChange >>>> date", date);
        if (date != null) {
            setBirth(() => date);
        }
        setTmpBirth(() => moment(date).format('YYYY-MM-DD'));
    };
    useEffect(() => {
        console.log("useEffect >>>> tmpBirth", tmpBirth);
        setInputs({ ...inputs, "birth": tmpBirth });
    }, [tmpBirth])
    //--- birth
    //--- gender
    const [genderValue, setGenderValue] = useState('female');
    const handleChangeGender = (event: React.ChangeEvent<HTMLInputElement>) => {
        setGenderValue(() => event.target.value);
    };
    useEffect(() => {
        console.log("genderValue", genderValue);
        setInputs({ ...inputs, "gender": genderValue });
    }, [genderValue])
    //--- gender

    return (
        <>
            <form onSubmit={onSubmit}>
                <div className="media-container-round-white-shadow-padding40" style={{ marginTop: 20 }}>
                    <div className="common-flex-row-reverse-wrap-vcenter">
                        {
                            edit === false ?
                                <ButtonSquareLineB type={0} onClick={() => onEdit()}>편집</ButtonSquareLineB>
                                :
                                <>
                                    <ButtonSquareLineB type={1} onClick={() => onEdit()}>취소</ButtonSquareLineB>
                                    {props.item.id !== 0 &&
                                        <ButtonSquareLineB type={1} onClick={() => props.clickDeleteEvent(props.item.id)}>삭제</ButtonSquareLineB>
                                    }
                                    <ButtonSquareLineB type={1} onClick={() => onSubmit()}>저장</ButtonSquareLineB>
                                </>
                        }
                    </div>
                    <div className="media-grid--col2__11">
                        <div className="box-cgc">
                            <div className="common-flex-row-wrap-vcenter">
                                <span className="font-bold-s27-c573300" style={{ marginLeft: 20, marginRight: 20 }}>
                                    어린이 정보
                                </span>
                                <img src="/asset/img/myinfo_icon_pencil1.svg" style={{ marginLeft: 0 }} />
                            </div>
                            <div className="sign-line"></div>
                            <div className="common-flex-row-wrap-vcenter" style={{ marginLeft: 20, marginRight: 20 }}>
                                <div style={{ width: 100 }}><span className="font-bold-s12-c000000">아이 이름*</span></div>
                                <div>
                                    <InputBase sx={{ ml: 1, flex: 1, width: '100%' }}
                                        placeholder="이름을 입력해주세요."
                                        {...register('name')}
                                        value={inputs.name || ""}
                                        onChange={onChange} />
                                    {errors.name && (
                                        <FormErrorMessage errorMessage={errors.name.message!} />
                                    )}
                                </div>
                            </div>

                            <div className="sign-line"></div>
                            <div className="common-flex-row-wrap-vcenter" style={{ marginLeft: 20, marginRight: 20 }}>
                                <div style={{ width: 100 }}><span className="font-bold-s12-c000000">아이 성별*</span></div>
                                <div>
                                    <RadioGroup
                                        row
                                        name="controlled-radio-buttons-group"
                                        value={genderValue}
                                        onChange={handleChangeGender}
                                    >
                                        <FormControlLabel {...register('gender')} value="female" control={<Radio size="small" />}
                                            label={<span style={{ fontSize: 13 }}>여성</span>} />
                                        <FormControlLabel {...register('gender')} value="male" control={<Radio size="small" />}
                                            label={<span style={{ fontSize: 13 }}>남성</span>} />
                                    </RadioGroup>
                                </div>
                            </div>
                            <div className="sign-line"></div>
                            <div className="common-flex-row-wrap-vcenter" style={{ marginLeft: 20, marginRight: 20 }}>
                                <div style={{ width: 100 }}><span className="font-bold-s12-c000000">아이 생년월일*</span></div>
                                <LocalizationProvider dateAdapter={AdapterDateFns}>
                                    <DesktopDatePicker
                                        label=""
                                        value={birth}
                                        inputFormat={"yyyy-MM-dd"}
                                        mask={"____-__-__"}
                                        {...register('birth')}
                                        onChange={(date) => handleBirthDateChange(date)}
                                        renderInput={({ inputRef, inputProps, InputProps }) => (
                                            <Box sx={{ display: 'flex', alignItems: 'center', flexGrow: 1 }}>
                                                <input ref={inputRef} {...inputProps} style={{ border: "none", flexGrow: 1, height: 30 }} />
                                                {InputProps?.endAdornment}
                                            </Box>
                                        )}
                                    />
                                </LocalizationProvider>
                                {errorMsg === "" ? <></> : <FormErrorMessage errorMessage={errorMsg} />}
                            </div>
                        </div>
                        <div className="box-cgc">
                            {/* <div className="common-flex-row-reverse-wrap-vcenter" style={{ marginRight: 20 }}>
                                <img src="/asset/img/treatment_icon_x.svg" />
                            </div> */}
                            <div className="common-flex-row-wrap-vcenter">
                                <span className="font-bold-s27-c573300" style={{ marginLeft: 20, marginRight: 20 }}>
                                    메모
                                </span>
                            </div>
                            <div style={{ marginTop: 10 }}>

                                <TextField
                                    fullWidth
                                    id="outlined-multiline-static"
                                    label=""
                                    {...register('memo')}
                                    value={inputs.memo || ""}
                                    onChange={onChange}
                                    multiline
                                    rows={10}
                                    defaultValue="치료일지"
                                    InputProps={{
                                        style: {
                                            paddingLeft: 30,
                                            backgroundColor: '#f9f9f9',
                                        },
                                        startAdornment: (
                                            <InputAdornment position="start" style={{ position: "absolute", top: 27, left: 10 }}>
                                                <img src="/asset/img/treatment_icon_pencil.svg" />
                                            </InputAdornment>
                                        )
                                    }}
                                />
                                {errors.memo && (
                                    <FormErrorMessage errorMessage={errors.memo.message!} />
                                )}
                            </div>
                        </div>
                    </div>
                </div>
            </form>
        </>
    )
}
export default ChildInfoLists

