import { InputAdornment, TextField } from "@mui/material"
import { useEffect, useState } from "react"
import { useRecoilState, useSetRecoilState } from "recoil"
import { ChipLarge } from "../../components"
import ButtonSquareSmall from "../../components/ButtonSquareSmall"
import GroupAddTitle from "./components/GroupAddTitle"
import { GroupAddStep, initGroupAddStep4BtnAtom, initGroupAddStepAtom, ListsSubject } from "../../api/atom.groupadd"


interface Step4Props {
    allEdit: boolean;
    linkGroupAddStep: (num: number) => void;
}

const Step4: React.FC<Step4Props> = (props) => {
    const [groupStepAtom, setGroupStepAtom] = useRecoilState<GroupAddStep>(initGroupAddStepAtom);
    const setStepUseSetRecoilState = useSetRecoilState(initGroupAddStepAtom);

    const [groupStep4BtnAtom, setGroupStep4BtnAtom] = useRecoilState<number[]>(initGroupAddStep4BtnAtom);
    const setStep4BtnUseSetRecoilState = useSetRecoilState(initGroupAddStep4BtnAtom);


    // --- menu memory btn
    const [menu, setMenu] = useState(1);
    const menuStr = ["", "언어치료", "응용행동분석(ABA)", "작업치료", "감각통합", "특수체육", "심리", "미술", "상담", "기타"];
    const [menuArray, setMenuArray] = useState(groupStep4BtnAtom);
    const changeMenu = (num: number) => {
        setMenu(() => num);
        const newArr = [...menuArray];
        menuArray.map((item: number, index: number) => {
            if (num == index) {
                if (item == 0) { newArr[index] = 1 }
                if (item == 1) { newArr[index] = 0 }
            }
            setMenuArray(() => newArr);
            //setStep4BtnUseSetRecoilState(() => newArr);
        });
    }
    const clickMenu = (num: number) => {
        changeMenu(num);
    };
    let strArray: { subject: string }[] = [];
    // menu memory btn ---

    const [inputText, setInputText] = useState(groupStepAtom.group_therapy_subject_response.etc);
    useEffect(() => {
        //console.log("menuArray", menuArray);
        strArray = [];
        menuArray.map((item: number, index: number) => {
            if (item == 1) {
                strArray.push({ "subject": menuStr[index] });
            }
        });
        //console.log("array", strArray);
        const newValue = { group_therapy_subject_response: { etc: inputText, therapy_subject_responses: strArray } }
        setStepUseSetRecoilState(() => ({ ...groupStepAtom, ...newValue }));
        setStep4BtnUseSetRecoilState(() => menuArray);
    }, [menuArray, inputText]);

    const onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        setInputText(() => e.target.value);
        //setErrorMessage("");
    }

    return (
        <>
            {/* 치료구분 - title */}
            <div className="common-flex-row-wrap-space-between-vcenter"
                style={{ marginTop: 20 }}>
                <div className="item-lr">
                    <div className="common-flex-row-wrap-vcenter">
                        <span className="font-bold-s21-c573300"><span className="span-half-FFE197">치료과목</span></span>
                    </div>
                </div>
                <div className="item-lr">
                    <div className="common-flex-row-wrap-vcenter">
                        {
                            props.allEdit === true &&
                            <ButtonSquareSmall background="#FF9100">저장</ButtonSquareSmall>
                        }
                        <ButtonSquareSmall background="#FF9100" onClick={() => props.linkGroupAddStep(3)}>이전</ButtonSquareSmall>
                        <ButtonSquareSmall background="#FF9100" onClick={() => props.linkGroupAddStep(5)}>다음</ButtonSquareSmall>
                    </div>
                </div>
            </div>

            <GroupAddTitle background="#A7BDDE" image="/asset/img/new_subject.svg">
                치료과목은 언어, 응용행동분석(ABA), 작업치료, 감각통합,
                특수 체육, 심리, 미술, 음악, 상담, 기타로 구성됩니다.<br />
                기타치료를 진행하시고자 하는 경우, 직접 입력해주시면 됩니다. <br />
                개별치료를 진행하는 경우, 하나의 과목을 선택할 수 있습니다. <br />
                그룹치료를 진행하는 경우, 다수의 선생님과 치료과목을 선택할 수 있습니다.
            </GroupAddTitle>


            <div className="media-group-grey-container" style={{ marginTop: 20 }}>
                <div className="common-flex-row-wrap-vcenter" style={{ marginTop: 10 }}>
                    <ChipLarge onClick={() => clickMenu(1)} type={menuArray[1]}>언어치료</ChipLarge>
                    <ChipLarge onClick={() => clickMenu(2)} type={menuArray[2]}>응용행동분석(ABA)</ChipLarge>
                    <ChipLarge onClick={() => clickMenu(3)} type={menuArray[3]}>작업치료</ChipLarge>
                    <ChipLarge onClick={() => clickMenu(4)} type={menuArray[4]}>감각통합</ChipLarge>
                    <ChipLarge onClick={() => clickMenu(5)} type={menuArray[5]}>특수체육</ChipLarge>
                    <ChipLarge onClick={() => clickMenu(6)} type={menuArray[6]}>심리</ChipLarge>
                    <ChipLarge onClick={() => clickMenu(7)} type={menuArray[7]}>미술</ChipLarge>
                    <ChipLarge onClick={() => clickMenu(8)} type={menuArray[8]}>상담</ChipLarge>
                    <ChipLarge onClick={() => clickMenu(9)} type={menuArray[9]}>기타</ChipLarge>
                </div>
                <div>
                    <TextField id="standard-basic" style={{ width: '100%' }}
                        label="타 치료과목을 입력해주세요." variant="standard"
                        value={inputText}
                        onChange={onChange}
                        InputProps={{
                            startAdornment: (
                                <InputAdornment position="start">
                                    <img src="/asset/img/treatment_icon_pencil.svg" />
                                </InputAdornment>
                            )
                        }} />
                </div>
            </div>
            <div className="media-blank-bottom"></div>
        </>

    )

}
export default Step4


