// 카드안에 들어가는 작은 칩
import React from "react";
import styled from "styled-components";

interface ChipSmallProps {
  children?: React.ReactNode;
  width?: number; 
  marginLeft?: number; 
  type?: number; 
  onClick?: ()=> void;
}

const ChipMedium: React.FC<ChipSmallProps> = (props) => {
  const { width, children, marginLeft, type, onClick } = props; 

  const Styled0 = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px 10px 5px 10px;
    margin: 0px 10px 10px 0px ;
    background: #FFFFFF;
    border: 1px solid #000000;
    box-sizing: border-box;
    border-radius: 17px;
    font-family: "Elice Digital Baeum",sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 10px;
    text-align: center;
    letter-spacing: 0.03em;
    color: #000000;
    margin-right: 4px;


    &:active,
    &:hover{
        background: #000000;
        color: #FFFFFF;
        cursor: pointer;
    }
    &:focus {
        background: var(--button-hover-bg-color, #FFFFFF);
        cursor: pointer;
    }
    
    &:disabled {
        cursor: default;
        opacity: 0.5;
        background: var(--button-bg-color, #FFFFFF);
    }
  `;

  const Styled1 = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px 10px 5px 10px;
    margin: 0px 10px 10px 0px ;
    background: #000000;
    border: 1px solid #000000;
    box-sizing: border-box;
    border-radius: 17px;
    font-family: "Elice Digital Baeum",sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 10px;
    text-align: center;
    letter-spacing: 0.03em;
    color: #ffffff;
    margin-right: 4px;
  `;

  return (
    <>
    {
        type === 0 ?
        <Styled0
        style={{
          width: `${width}px`,    
          marginLeft: `${marginLeft}px`,  
        }}
              onClick = {onClick}
      >
        {children}
      </Styled0>
        :
        <Styled1
        style={{
          width: `${width}px`,    
          marginLeft: `${marginLeft}px`,  
        }}
              onClick = {onClick}
      >
        {children}
      </Styled1>

    }
    </>
  );
};

ChipMedium.defaultProps ={
    type:0
  }

export default ChipMedium

