import React from "react";
import styled from "styled-components";

interface GroupAddTitleProps {
    children?: React.ReactNode;
    background?: string;
    image?: string;
}

const GroupAddTitle: React.FC<GroupAddTitleProps> = (props) => {
    const { children, background, image } = props;



    const Styled = styled.div`
        background: ${background};
        border: 2px solid #573300;
        box-sizing: border-box;
        border-radius: 14px;
        margin-top: 20px;
    `;

    return (
        <>
            <Styled
                style={{
                    background: `${background}`,
                }}
            >
                <div className="common-flex-row-wrap-vcenter" style={{ marginTop: 20, marginBottom: 20 }}>
                    <img src={image} className="media-group-add-image-none"
                        style={{ width: 180, marginLeft: 30, marginTop: 0, marginBottom: 4 }} />
                    <div className="common-flex-column-wrap">
                        <div className="group-content" style={{ marginTop: 0 }}>
                            <span className="font-bold-s15-c000000"
                                style={{ marginLeft: 20, marginRight: 20 }}>
                                {children}
                            </span>
                        </div>
                    </div>
                </div>
            </Styled>
        </>
    );
};

GroupAddTitle.defaultProps = {
    background: "#FFEFCB",
    image: "/asset/img/new_classify_03.svg"
}

export default GroupAddTitle