import { Avatar } from "@mui/material";
import React, {
    ChangeEvent,
    useCallback,
    useRef,
    useState,
    useEffect
} from "react";
import { Post } from "../../api/api";
import "./DragDropInput.scss";

export interface IFileTypes {
    id: number;
    object: File;
}

interface Props {
    //fileInput: IFileTypes;
}

const DragDropInput: React.FC<Props> = (props) => {
    // const DragDropInput = () => {
    const [isDragging, setIsDragging] = useState<boolean>(false);
    const [files, setFiles] = useState<IFileTypes[]>([]);

    const dragRef = useRef<HTMLLabelElement | null>(null);
    const fileId = useRef<number>(0);

    const onChangeFiles = useCallback(
        (e: ChangeEvent<HTMLInputElement> | any): void => {
            let selectFiles: File[] = [];
            let tempFiles: IFileTypes[] = files;

            if (e.type === "drop") {
                selectFiles = e.dataTransfer.files;
            } else {
                selectFiles = e.target.files;
            }
            for (const file of selectFiles) {
                tempFiles = [
                    ...tempFiles,
                    {
                        id: fileId.current++,
                        object: file
                    }
                ];
            }
            setFiles(tempFiles);
            console.log("file", e.target.files[0]);
            const uploadFile = e.target.files[0];
            const formData = new FormData();
            formData.append('multipartFile', uploadFile);
            formData.append('surveyScore', '3');
            formData.append('duration', 'PT1S');
            console.log("multipartFile", uploadFile);
            console.log("formData", formData.get("surveyScore"));
            Post.signUpFile(formData)
                .then((data) => {
                    console.log("signUpFile", data);
                })
                .catch((err) => {
                    console.log("signUpFile error", err);
                });

            // Post.getGroupCards('치료', '언어치료,작업치료', 0, 'id,desc', 10)
            //     .then((data) => {
            //         console.log("getPosts", data);
            //     })
            //     .catch((err) => {
            //         console.log("error", err);
            //     });

        },
        [files]
    );

    const handleFilterFile = useCallback(
        (id: number): void => {
            setFiles(files.filter((file: IFileTypes) => file.id !== id));
        },
        [files]
    );

    const handleDragIn = useCallback((e: DragEvent): void => {
        e.preventDefault();
        e.stopPropagation();
    }, []);

    const handleDragOut = useCallback((e: DragEvent): void => {
        e.preventDefault();
        e.stopPropagation();

        setIsDragging(false);
    }, []);

    const handleDragOver = useCallback((e: DragEvent): void => {
        e.preventDefault();
        e.stopPropagation();

        if (e.dataTransfer!.files) {
            setIsDragging(true);
        }
    }, []);

    const handleDrop = useCallback(
        (e: DragEvent): void => {
            e.preventDefault();
            e.stopPropagation();

            onChangeFiles(e);
            setIsDragging(false);
            // let imageFile = e.dataTransfer!.files[0];
            // setPreviewUrl(URL.createObjectURL(imageFile));
        },
        [onChangeFiles]
    );

    const initDragEvents = useCallback((): void => {
        if (dragRef.current !== null) {
            dragRef.current.addEventListener("dragenter", handleDragIn);
            dragRef.current.addEventListener("dragleave", handleDragOut);
            dragRef.current.addEventListener("dragover", handleDragOver);
            dragRef.current.addEventListener("drop", handleDrop);
        }
    }, [handleDragIn, handleDragOut, handleDragOver, handleDrop]);

    const resetDragEvents = useCallback((): void => {
        if (dragRef.current !== null) {
            dragRef.current.removeEventListener("dragenter", handleDragIn);
            dragRef.current.removeEventListener("dragleave", handleDragOut);
            dragRef.current.removeEventListener("dragover", handleDragOver);
            dragRef.current.removeEventListener("drop", handleDrop);
        }
    }, [handleDragIn, handleDragOut, handleDragOver, handleDrop]);

    useEffect(() => {
        initDragEvents();

        return () => resetDragEvents();
    }, [initDragEvents, resetDragEvents]);

    return (
        <div>
            <input
                type="file"
                id="fileUpload"
                style={{ display: "none" }}
                multiple={false}
                onChange={onChangeFiles}
            />

            <label
                className={isDragging ? "DragDropInput-File-Dragging" : "DragDropInput-File"}
                htmlFor="fileUpload"
                ref={dragRef}
            >
                <div className="common-flex-row-wrap-hcenter-vcenter">
                    <img src="/asset/img/myinfo_teacher_Icon_metro-attachment.svg"
                        style={{ marginRight: 10 }} />
                    자격증 첨부, 기관회원의 경우 사업자등록증을 첨부해주세요.
                    <span className="font-normal-s12-c666666" style={{ marginTop: 0 }}>
                        클릭하거나 마우스로 드래그해서 파일을 추가해주세요.</span>
                </div>
            </label>

            <div className="DragDropInput-Files">
                {files.length > 0 &&
                    files.map((file: IFileTypes) => {
                        const {
                            id,
                            object: { name }
                        } = file;

                        return (
                            <div key={id}>
                                <div>{name}</div>
                                <div
                                    className="DragDropInput-Files-Filter"
                                    onClick={() => handleFilterFile(id)}
                                >
                                    X
                                </div>
                            </div>
                        );
                    })}
            </div>
        </div>
    );
};

export default DragDropInput;