import React, { useEffect, useState } from 'react'
import { initMemoLists, initScheduleLists, MemoLists, ScheduleLists } from '../../../api/atom.myinfo';
import ButtonSquareLineB from '../../../components/ButtonSquareLineB'
import ScheduleListEditor from './ScheduleListEditor';
import MemoListEditor from './MemoListEditor';

interface Props {
    calendar: string;
}

const ScheduleMemo: React.FC<Props> = (props) => {

    const [scheduleLists, setScheduleLists] = useState<ScheduleLists[]>(initScheduleLists);
    const changeScheduleLists = (data: ScheduleLists[]) => {
        setScheduleLists(() => data);
    };
    const [memoLists, setMemoLists] = useState<MemoLists[]>(initMemoLists);
    const changeMemoLists = (data: MemoLists[]) => {
        setMemoLists(() => data);
    };
    const [edit, setEdit] = useState(false);
    const onEdit = () => {
        setEdit(() => !edit);
    }
    useEffect(() => {
        if (edit == false) {
            console.log("server get");
        }
    }, [props.calendar, edit]);

    const onScheduleMemoSave = () => {
        if (edit == true) {
            console.log("server post");
            setEdit(() => !edit);
        }
    }
    return (
        <div className="media-container-round-white-shadow-padding20">
            <div className="common-flex-row-wrap-space-between-vcenter" style={{ padding: 10, height: 34 }}>
                <div>
                    <img src="/asset/img/group_detail_icon_people.svg" style={{ marginRight: 10 }} />
                    <span className="font-normal-s15-c573300">{props.calendar}</span>
                </div>
                {
                    edit === false ?
                        <ButtonSquareLineB type={0} onClick={() => onEdit()}>편집</ButtonSquareLineB>
                        :
                        <ButtonSquareLineB type={1} onClick={() => onScheduleMemoSave()}>저장</ButtonSquareLineB>
                }
            </div>
            <div className="media-grid--col2__11">
                <div className="box-cgc">
                    <div className="myinfo-container">
                        <span className="font-normal-s12-c573300-Fredoka" style={{ marginLeft: 10, marginTop: 10, marginBottom: 10 }}>
                            <span className="span-half-2">SCHEDULE</span>
                        </span>
                        {
                            edit === false ?
                                scheduleLists.map((item, index) => (
                                    <div key={"root" + item.id}>
                                        <div key={item.id} className="group-card-title-margin">
                                            {
                                                item.check === false ?
                                                    <>
                                                        <img key={"img" + item.id} src="/asset/img/myinfo_icon_nocheck_calendar.svg" />
                                                        <span key={"span" + item.id} className="font-bold-s12-c000000" style={{ marginLeft: 10 }}>
                                                            {item.time} - {item.text}</span>
                                                    </>
                                                    :
                                                    <>
                                                        <img key={"img" + item.id} src="/asset/img/myinfo_icon_check_calendar.svg" />
                                                        <span key={"span" + item.id} className="font-bold-s12-c808080" style={{ marginLeft: 10 }}>
                                                            {item.time} - {item.text}</span>
                                                    </>
                                            }

                                        </div>
                                        <div key={"line" + item.id} className="group-line" style={{ marginBottom: 5 }}></div>
                                    </div>
                                ))
                                :
                                <div className="group-card-title-margin">
                                    <ScheduleListEditor lists={scheduleLists} changeLists={changeScheduleLists}></ScheduleListEditor>
                                </div>

                        }
                    </div>
                </div>


                <div className="box-cgc">
                    <div className="myinfo-container">
                        <span className="font-normal-s12-c573300-Fredoka" style={{ marginLeft: 10, marginTop: 10, marginBottom: 10 }}>
                            <span className="span-half-2">MEMO</span></span>


                        {
                            edit === false ?
                                memoLists.map((item, index) => (
                                    <div key={"root" + item.id}>
                                        <div key={item.id} className="group-card-title-margin">
                                            {
                                                item.check === false ?
                                                    <>
                                                        <img key={"img" + item.id} src="/asset/img/myinfo_icon_black_square.svg" />
                                                        <span key={"span" + item.id} className="font-bold-s12-c000000" style={{ marginLeft: 10 }}>
                                                            {item.text}</span>
                                                    </>
                                                    :
                                                    <>
                                                        <img key={"img" + item.id} src="/asset/img/myinfo_icon_gray_check-square.svg" />
                                                        <span key={"span" + item.id} className="font-bold-s12-c808080-line" style={{ marginLeft: 10 }}>
                                                            {item.text}</span>
                                                    </>
                                            }

                                        </div>
                                        <div key={"line" + item.id} className="group-line" style={{ marginBottom: 5 }}></div>
                                    </div>
                                ))
                                :
                                <div className="group-card-title-margin">
                                    <MemoListEditor lists={memoLists} changeLists={changeMemoLists}></MemoListEditor>
                                </div>

                        }

                    </div>
                </div>
            </div>


        </div>
    )
}

export default ScheduleMemo